/* eslint-disable no-script-url,jsx-a11y/anchor-is-valid */
import React, { Component } from "react";
import { connect } from "react-redux";
import { v4 as uuidv4 } from 'uuid';
import SVG from "react-inlinesvg";
import { Button, OverlayTrigger, Tooltip } from "react-bootstrap";         
import { Link } from "react-router-dom";
import { ActiveUserSkeleton } from "../../analytics-new/elements/skeleton-loader-cards/SkeletonCards";
import DataLoading from "../../analytics-new/elements/skeleton-loader-cards/loader";
import DataNotFound from "../../analytics-new/elements/skeleton-loader-cards/notfound";
import { kFormatter, toAbsoluteUrl } from "../../../../utils";

export default class ActiveUsers extends Component {

 
  render() {
    const {  top_creator, top_creator_loader , totalPostCounts} = this.props; 
    return (
      <div className="dashboard_activeCrators overview_mostCreater"> 
        <div className="card db_activeCrators_popularChart chartBox"> 
            <div className="d-flex align-items-center justify-content-between db_activeCrators_cardHeading">
              <h4>Most Active Creators</h4>
              <div className="bs_box_icon">
                  <OverlayTrigger placement='top' overlay={ <Tooltip>Multiply your social UGC by collaborating with your top listed Creators </Tooltip> } >
                  <img src="https://cloud.taggbox.com/taggshop/app/assets/media/dashboard/icons/tooltip-icon.svg" alt="tooltip-icon" />
                  </OverlayTrigger> 
              </div>
            </div>
            
            <div className="card-body p-0 mb-5">
              {(top_creator && top_creator.data && top_creator.data.length > 0) ? top_creator.data.map((mItem, mIndex) => <div className="d-flex px-5 px-xxl-5 py-3 db_activeCrators_usersRow justify-content-between" key={uuidv4()}>   
                  <div className="d-flex align-items-center db_activeCrators_macLeft">
                    <div className="symbol symbol-45 symbol-light mr-sm-6 mr-3 db_activeCrators_conctUserIimg cursor-pointer">
                      <span className="no-repeat-fit-background" style={{ backgroundImage: `url(${mItem.image.replace(/\s/g, '')}),url(https://cloud.taggbox.com/analytics/media/svg/dashboard/no-image.svg)` }}></span>
                    </div>
                    <div className="d-flex text-left flex-column db_activeCrators_connUserInfo cursor-pointer">
                      <h6 className="text-dark mb-0 font-weight-boldest overflow-hidden text-nowrap text-ellipsis db_activeCrators_authorName">{mItem.author.name}</h6>
                      <p className="m-0">
                        <small className="text-muted overflow-hidden text-nowrap text-ellipsis db_activeCrators_postCount mr-2">{kFormatter(mItem.postCount)} {mItem.postCount && parseInt(mItem.postCount) > 1 ? `Posts` : `Post`} </small> 
                      </p>
                    </div>
                  </div>
                  <div className="db_activeCrators_listCategory_icon cursor-pointer d-flex align-items-center pt-1">
                  <div className="ts_ms_slider-social-icon" style={{fontSize: '18px'}}><div className={`ts__icon ts-${mItem?.network?.icon && mItem?.network?.icon.includes('fa-') ? mItem?.network?.icon.replace('fa-', '') :''}`}><div> </div></div></div> 
                  </div>
                  <div className="db_activeCrators_connStatus db_activeCrators_connnected d-flex align-items-center">
                    <div className="db_activeCrators_connStatus_wrap">
                    <Link to={`/creators${mItem.author?.username ? '?text='+mItem.author.username :''}`}><Button variant="btn-min-w btn creatorButton"> Connect</Button></Link>
                    </div>
                  </div>
                </div>
                ) : 
                <div className="data-not-found-container">
                  <div className="d-flex align-items-center justify-content-center flex-column text-center dataNotFound-inner">
                    <h4>Find your Creators</h4>
                    <p>Nothing to see? Partner with top UGC creators with our Creator Collaborator program.</p>
                    <div className="not_conne_overlay d-flex flex-column flex-center">
                      <Link className="btn btn-primary" to={`/creators`}>
                        Collab with Creators
                      </Link>
                    </div>
                  </div>
                </div>} 
            </div>
        </div>
      </div>
    );
  }
}

 