import React, { Component } from 'react'
import ReactApexChart from 'react-apexcharts' 
    export default class ugcbrandedContent extends Component {
    constructor(props) {
      super(props);

      this.state = {
      
        series: this.props?.ugc[0].data, 
        options: {
            chart: { type: 'donut',  },
            plotOptions:    { pie: 
                                {   donut: { 
                                    legend: { show: false, }, 
                                    labels: { show: true, total: { showAlways: true, show: false } ,  } 
                                    }
                                } 
                            },
            dataLabels: { enabled: false },
            colors: this.props.color,
            legend: { show: false, position: 'right' },  
            labels: this.props?.ugc[0].labels,  

            responsive: [
              { breakpoint: 1860, options: { chart: { width: 380, }, }, },
              { breakpoint: 1600, options: { chart: { width: 320, }, }, },
              { breakpoint: 1400, options: { chart: { width: 300, }, }, },
              { breakpoint: 1200, options: { chart: { width: 250, }, }, },
              { breakpoint: 992, options: { chart: { width: 600, }, }, },
              { breakpoint: 768, options: { chart: { width: 500, }, }, },
              { breakpoint: 576, options: { chart: { width: 400, }, }, },
              { breakpoint: 480, options: { chart: { width: 300, }, }, },
            ],
        }, 
      };
    }


  componentDidUpdate(prevProps){
    if(prevProps.ugc[0].data != this.props?.ugc[0].data ){
      this.setState({  series: this.props?.ugc[0].data, })
    }
  }

    render() {
      const {ugc } = this.props
      return (
        <div className='row align-items-center h-100'>
            <div className="col-sm-12 col-lg-6">
              <div id="chart" className="dashboardUGCChart"> 
                <ReactApexChart options={this.state.options} series={this.state.series} type="pie" width={450} /> 
              </div>
            </div>
            <div className="col-sm-12 col-lg-6">
              
                <div className="d-flex align-items-center dashboard_chart_rightAlign">
                  <div className="dashboard_chartColumn">
                    <div className="dashboard_chartLabel">
                      {ugc && ugc[0] ? ugc[0]?.labels[0] :'UGC posts' }
                    </div>
                    <div className="dashboard_chartValue">
                      {ugc && ugc[0] ? ugc[0]?.data[0] :'' }
                    </div>
                  </div> 
                  <div className="dashboard_chartColumn">
                    <div className="dashboard_chartLabel">
                      {ugc && ugc[0] ? ugc[0]?.labels[1] :'UGC posts' }
                    </div>
                    <div className="dashboard_chartValue">
                      {ugc && ugc[0] ? ugc[0]?.data[1] :'' }
                    </div>
                  </div>
                </div>
              
            </div>
          </div>
      );
    }
  }



