import React, { Component } from 'react'
import LoadingScreen from '../../../pages/LoadingScreen';
import { CloudHostUrl } from '../../../../actions/api';

export default class ChooseNetwork extends Component {
    state = { addclass: true, };

    componentDidMount() {
        const { addFeedData } = this.props

    }

    render() {
        const { addclass } = this.state;
        const { addFeedData, onNetworkSelect } = this.props
        console.log('addFeedData', addFeedData)
        return (
            <div className="onboarding_addFeed">
                <div className="small_container">
                    {addFeedData && addFeedData.length ?
                        <div className="justify-content-center align-items-center p-5">
                            <div className="text-center obAddFeedHead">
                                <h3 className="obNetworkTitle">Choose Source Network</h3>
                                <p className="obNetworkSubtext">Select the social platform from which you want to add the feeds</p>
                            </div>
                            <ul className={`obSourceNetworks ${addclass ? "" : "allNetworks"}`}>
                                {addFeedData.map((network, index) => (
                                    network.networkId != 33 && network.networkId != 29 ? <li className={network.networkCode} key={network.networkId}>
                                        <a className="popUp resetAction" onClick={() => onNetworkSelect(network.networkId)}>
                                            <img src={`${CloudHostUrl}taggshop/app/assets/media/square-icons/${network.networkCode == "collab-with-creators" ? "creators-collab" : network.networkCode}.svg?ver=1`} alt={network.networkName} className="img-fluid" />
                                            <span>{network.networkName}</span>
                                        </a>
                                    </li> : <></>
                                ))}
                                {/* <li className="instagram-business">
                                    <a className="popUp resetAction">
                                        <img src="https://cloud.taggbox.com/taggshop/app/assets/media/square-icons/instagram-business.svg?ver=1" alt="Instagram" className="img-fluid" />
                                        <span>Instagram</span>
                                    </a>
                                </li> */}

                            </ul>
                            {addFeedData.length >=5 && addclass ? <button className="showAllNetworks_btn" onClick={() => this.setState({ addclass: !addclass })}>
                                <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" viewBox="0 0 16 16" fill="none">
                                    <path d="M8 0C12.4062 0 16 3.59375 16 8C16 12.4375 12.4062 16 8 16C3.5625 16 0 12.4375 0 8C0 3.59375 3.5625 0 8 0ZM8 14.5C11.5625 14.5 14.5 11.5938 14.5 8C14.5 4.4375 11.5625 1.5 8 1.5C4.40625 1.5 1.5 4.4375 1.5 8C1.5 11.5938 4.40625 14.5 8 14.5ZM8 9.5C7.5625 9.5 7.25 9.1875 7.25 8.75V4.75C7.25 4.34375 7.5625 4 8 4C8.40625 4 8.75 4.34375 8.75 4.75V8.75C8.75 9.1875 8.40625 9.5 8 9.5ZM8 10.5625C8.53125 10.5625 8.96875 11 8.96875 11.5312C8.96875 12.0625 8.53125 12.5 8 12.5C7.4375 12.5 7 12.0625 7 11.5312C7 11 7.4375 10.5625 8 10.5625Z" fill="#4E69ED" />
                                </svg>
                                Click here to see all supported Networks
                            </button> : <></>}
                        </div>
                        :
                        <LoadingScreen />
                    }
                </div>
            </div>
        )
    }
}
