import React, { Component } from 'react'
import { CREATE_OB_FEED, CREATE_OB_FEED_FB, CREATE_OB_ONSITE_FEED } from '../../../../actions/onBoradingAction'
import { SHOW_MORE_TAB } from '../constant'
import { HOST_URL } from '../../../../actions/api'
import SearchPlace from './elements/searchPlace'
import FbPageAutoComplete from './elements/FbPageAutoComplete'
import YoutubeInput from './elements/youtubeInput'

export default class FeedPage extends Component {
    state = { activeFilter: "", feedText: "", placeId: '', placeName: '', showError: false, channelId: '', feedTextCharError: false, filterName: "", isLoading: false, latlng: "", keyword: "" }
    componentDidMount() {
        if (this.props.contentObject.filters && this.props.contentObject.filters.length) this.setState({ activeFilter: this.props.contentObject.filters[0].filter, filterName: this.props.contentObject.filters[0].feedtype })
    }

    componentDidUpdate(prevProps, prevState) {
        if (prevProps.contentObject.filters != this.props.contentObject.filters) {
            if (this.props.contentObject.filters && this.props.contentObject.filters.length) this.setState({ activeFilter: this.props.contentObject.filters[0].filter, filterName: this.props.contentObject.filters[0].feedtype })
        }
    }

    onFieldTextChange = (text, id, placeName) => this.setState({ feedText: text, placeId: id, placeName: placeName, showError: false })

    onInputFeedText = (e) => {
        if (this.props.selectedNetworkId == 28 && this.state.activeFilter == 70) this.setState({ feedText: e.target.value, error: false, feedTextCharError: false })
        else {
            if (e.target.value.indexOf("@") > -1 || e.target.value.indexOf("#") > -1) { this.setState({ feedTextCharError: true, error: false }); return }
            else if (!e.target.value || e.target.value == "") this.setState({ error: true, feedTextCharError: false, feedText: "" })
            else this.setState({ feedText: e.target.value, error: false, feedTextCharError: false })
        }
    }
    onInputFb = (value) => {
        this.setState({ feedText: value, error: false })
    }
    onKeywordChange = (value) => this.setState({ keyword: value })

    onChannelIdChange = (value) => this.setState({ channelId: value })
    onLoading = (value) => this.setState({ isLoading: value })
    onLatlanChange = (value) => this.setState({ latlng: value })
    clearFeedTextOnBack = () => this.setState({ feedText: "", error: false, activeFilter: "" }, () => this.props.onBack())

    onAddFeed = () => {
        const { activeFilter, feedText, channelId, placeId, placeName, latlng, keywords } = this.state
        const { selectedNetworkId, contentObject, onNext } = this.props
        this.onLoading(true)
        if (selectedNetworkId == 29) {
            const obj = {
                network: 29,
                filter: 2,
                moderation: 0,
                onsite_qr_code_btn_text: "Scan QR code or click here to submit reviews/rating",
                onsite_btn_color: "#000000",
                onsite_btn_txt_color: "#ffffff",
                onsite_link_font_btn: "Lato",
                onsite_css_font_btn: "Lato",
                onsite_font_varient_btn: "",
                btn_fontSize: 16,
                onsite_qr_code_status: 1,
                onsite_popup_title: "Submit your posts",
                onsite_popup_btn_txt: "Submit post",
                onsite_popup_sub_heading: "You can share your reviews/rating here",
                onsite_term_url: "https://taggbox.com/on-site-uploads-content-rights/",
                onsiteImgBack: "",
                onsite_logo: "",
                Onsite_logoImageValue: "",
                onsite_bg_color_wg: "#E8E8E8",
                onsiteBackImage: "",
                onsite_bg_image_wg: "",
                Onsite_backImageValue: "",
                onsite_succ_msg: "Thanks for the submission. Your post will live on the website after approval.",
                onsite_thank_txt_color: "#000000",
                onsite_link_font_msg: "Default",
                onsite_css_font_msg: "Default",
                onsite_font_varient_msg: "",
                onsite_thank_fontSize: 16,
                wallId: localStorage.getItem('wallId')
            }
            CREATE_OB_ONSITE_FEED(obj, onNext, this.onLoading)
        }
        else {
            if (feedText == "" || !feedText) {
                this.setState({ error: true })
                this.onLoading(false)
                return false
            }
            var obj = {
                wallId: localStorage.getItem('wallId'),
                networkId: selectedNetworkId,
                moderation: 0,
                account: 0,
                feedText: feedText,
                hashtagCaption: 0,
                filter: activeFilter ? activeFilter : contentObject.filters.length ? contentObject.filters[0].filter : null,
                accountId: '',
                hashtagIGTV: 0,
                onlyReels: 0,
            }
            if (selectedNetworkId == 3) {
                obj['field'] = feedText
                CREATE_OB_FEED_FB(obj, onNext, this.onLoading)
            }
            else if (selectedNetworkId == 23) {
                obj['airbnb_page_review'] = feedText
                CREATE_OB_FEED(obj, onNext, this.onLoading)
            }
            else if (selectedNetworkId == 7) {
                obj['channelId'] = channelId
                obj['latlng'] = latlng;
                obj['keywords'] = keywords;
                CREATE_OB_FEED(obj, onNext, this.onLoading)
            }
            else if (selectedNetworkId == 5) {
                obj['field1'] = feedText
                obj['field12boardurl'] = feedText
                CREATE_OB_FEED(obj, onNext, this.onLoading)
            }
            else if (selectedNetworkId == 6) {
                obj['field2'] = feedText
                obj['field1'] = feedText
                CREATE_OB_FEED(obj, onNext, this.onLoading)
                // console.log('obj', obj)
            }
            else if (selectedNetworkId == 4) {
                obj['field33'] = feedText
                obj['field33placeId'] = placeId
                obj['field33placeName'] = placeName
                CREATE_OB_FEED(obj, onNext, this.onLoading)
            }
            else if (selectedNetworkId == 28 && activeFilter == 70) {
                obj['tiktok_post_url'] = feedText
                CREATE_OB_FEED(obj, onNext, this.onLoading)
            }
            else if (selectedNetworkId == 1) {
                obj['excludeRetweet'] = 1
                obj['excludeReplies'] = 1
                CREATE_OB_FEED(obj, onNext, this.onLoading)
            }
            else {
                CREATE_OB_FEED(obj, onNext, this.onLoading)
            }
        }
    }

    render() {
        const { activeFilter, feedText, error, feedTextCharError, filterName, isLoading, keyword } = this.state
        const { contentObject, onBack, onNext, selectedNetworkId, onMoreTab } = this.props
        return (
            <div className="addfeedContainer">
                <div className="card card-custom card-body">
                    <div className="d-flex flex-wrap align-items-center mb-10 modal_head">
                        <div className="symbol symbol-40 flex-shrink-0 mr-4">
                            <div className="symbol-label" style={{ backgroundImage: `url(https://cloud.taggbox.com/taggshop/app/assets/media/square-icons/${contentObject.icon}.svg?ver=1)` }}></div>
                        </div>
                        <h3 className="d-flex flex-column flex-grow-1 my-lg-0 my-2 pr-3">
                            <span className="card-label font-weight-bolder text-dark networkTitle">{contentObject.name}</span>
                            <span className=" mt-2 font-weight-bold font-size-sm networkDesc">Add your {contentObject.name}
                                <span id="changeTxt"> {filterName}</span>
                            </span>
                        </h3>
                    </div>
                    <div className="form-body create_feed_wt">
                        <div className="tabbable-tabdrop newRadio d-flex align-items-center justify-content-between flex-wrap feeds_details personalizePopup">
                            <ul className="nav-tabs-line nav-tabs-bold w-100 tabDrop mb-5 nav nav-tabs">
                                {contentObject.filters && contentObject.filters.length ? contentObject.filters.map((filter, index) => (
                                    <li className="nav-item" role="presentation">
                                        <button className={`nav-link ${index == 0 ? 'active' : ''}`} id={`CreateFeedTab-tab-${filter.feedtype}`} data-bs-toggle="tab" data-bs-target="#home" type="button" role="tab" aria-controls="CreateFeedTab-tabpane-hashtag" aria-selected="true" onClick={() => this.setState({ activeFilter: filter.filter, feedText: '', filterName: filter.feedtype })}>
                                            <span>{filter.feedName}</span>
                                        </button>
                                    </li>
                                ))
                                    :
                                    <></>}
                                {SHOW_MORE_TAB(selectedNetworkId) ? <li className="nav-item" role="presentation">
                                    <button className="nav-link" id="messages-tab" onClick={onMoreTab}>
                                        <span>More</span>
                                    </button>
                                </li> : <></>}
                            </ul>
                            {selectedNetworkId == 29 ?
                                // --------------------for SnapUp---------------
                                <div className='tab-content d-flex'>
                                    <div className="alert alert-custom fade show mt-2 mb-0 bg-light rounded-0 p-4 align-items-start w-100">
                                        <div className="alert-icon py-1">
                                            <span className="svg-icon svg-icon-md">
                                                <svg xmlns="http://www.w3.org/2000/svg" width="13" height="13" viewBox="0 0 13 13">
                                                    <path id="Path_1660" fill="#959595" data-name="Path 1660" d="M17.5,11A6.5,6.5,0,0,0,11,17.5a6.64,6.64,0,0,0,2.472,5.1A6.265,6.265,0,0,0,17.5,24a6.5,6.5,0,0,0,0-13Zm.667,9.167a.667.667,0,0,1-1.333,0V17a.667.667,0,1,1,1.333,0ZM17.5,15.5a.667.667,0,1,1,.667-.667A.667.667,0,0,1,17.5,15.5Z" transform="translate(-11 -11)" opacity="0.999"></path>
                                                </svg>
                                            </span>
                                        </div>
                                        <div className="alert-text">
                                            Review &amp; Rating Feed will initially add a QR code or Button on your Gallery Header. Then, users can click on the Button or scan code with other devices to submit reviews &amp; ratings directly to your website and other social feeds.
                                        </div>
                                    </div>
                                    <div className="alert alert-custom fade show mt-2 mb-0 bg-light rounded-0 p-4 align-items-start w-100">
                                        <div className="alert-icon py-1">
                                            <span className="svg-icon svg-icon-md">
                                                <svg xmlns="http://www.w3.org/2000/svg" width="13" height="13" viewBox="0 0 13 13">
                                                    <path id="Path_1660" fill="#959595" data-name="Path 1660" d="M17.5,11A6.5,6.5,0,0,0,11,17.5a6.64,6.64,0,0,0,2.472,5.1A6.265,6.265,0,0,0,17.5,24a6.5,6.5,0,0,0,0-13Zm.667,9.167a.667.667,0,0,1-1.333,0V17a.667.667,0,1,1,1.333,0ZM17.5,15.5a.667.667,0,1,1,.667-.667A.667.667,0,0,1,17.5,15.5Z" transform="translate(-11 -11)" opacity="0.999"></path>
                                                </svg>
                                            </span>
                                        </div>
                                        <div className="alert-text">
                                            The Review &amp; rating section can be personalized using the Customization option later.
                                        </div>
                                    </div>
                                    <div className="alert alert-custom fade show mt-2 mb-0 bg-light rounded-0 p-4 align-items-start w-100">
                                        <div className="alert-icon py-1">
                                            <span className="svg-icon svg-icon-md">
                                                <svg xmlns="http://www.w3.org/2000/svg" width="13" height="13" viewBox="0 0 13 13">
                                                    <path id="Path_1660" fill="#959595" data-name="Path 1660" d="M17.5,11A6.5,6.5,0,0,0,11,17.5a6.64,6.64,0,0,0,2.472,5.1A6.265,6.265,0,0,0,17.5,24a6.5,6.5,0,0,0,0-13Zm.667,9.167a.667.667,0,0,1-1.333,0V17a.667.667,0,1,1,1.333,0ZM17.5,15.5a.667.667,0,1,1,.667-.667A.667.667,0,0,1,17.5,15.5Z" transform="translate(-11 -11)" opacity="0.999"></path>
                                                </svg>
                                            </span>
                                        </div>
                                        <div className="alert-text">
                                            Only 1 SnapUp feed is allowed per gallery
                                        </div>
                                    </div>
                                    <div className="mt-5 mb-10">
                                        <p>
                                            <strong>Share this URL to allow users to submit reviews on other marketing channels.</strong>
                                            <a href={`${HOST_URL}s/${localStorage.getItem('wallId')}/onsite-upload`}>{`${HOST_URL}s/${localStorage.getItem('wallId')}/onsite-upload`}</a>
                                        </p>
                                    </div>
                                </div>
                                :
                                // ----------------------for other network------------
                                <div className="tab-content">
                                    {contentObject.filters && contentObject.filters.length ? contentObject.filters.map((filter, index) => (
                                        <div role="tabpanel" id={`CreateFeedTab-tabpane-${filter.feedtype}`} aria-labelledby={`CreateFeedTab-tabpane-${filter.feedtype}`} className={`fade nav-link className= p-0 tab-pane ${activeFilter ? activeFilter == filter.filter ? 'active show' : '' : index == 0 ? 'active show' : ''}`}>
                                            {selectedNetworkId == 4 ?
                                                <>
                                                    <SearchPlace onFieldTextChange={this.onFieldTextChange} />
                                                    {error ? <span id="" className="errorMessage required">This field is required</span> : null}
                                                </>
                                                : selectedNetworkId == 3 ?
                                                    <FbPageAutoComplete text={feedText} onTextChange={this.onInputFb} error={error} />
                                                    : selectedNetworkId == 7 ?
                                                        <YoutubeInput tab={activeFilter} onValueChange={this.onInputFb} onChannelIdChange={this.onChannelIdChange} onLatlanChange={this.onLatlanChange} keyword={keyword} onKeywordChange={this.onKeywordChange}  />
                                                        :
                                                        <div className="form-group">
                                                            <div className="input-group">
                                                                <div className="input-group-prepend">
                                                                    <span className="input-group-text">
                                                                        <i className={`fas fa-${filter.icon}`}></i>
                                                                    </span>
                                                                </div>
                                                                <input data-limit="100" placeholder={`Enter ${filter.feedtype}`} className="autosearch mycheck form-control" id={`${filter.feedtype}-text`} type="text" value={feedText} onChange={this.onInputFeedText} />
                                                            </div>
                                                            {error ? <span id="" className="errorMessage required">This field is required</span> : null}
                                                            {feedTextCharError ? <span id="" className="errorMessage required">@ and # not allowed</span> : null}
                                                        </div>}
                                        </div>
                                    )) : <></>}
                                </div>}
                        </div>
                    </div>
                    <div className="obSyncProduct_tooltip obAddFeed_tooltip">
                        <div className="obInfobox">
                            <svg xmlns="http://www.w3.org/2000/svg" width="12" height="17" viewBox="0 0 12 17" fill="none">
                                <path d="M3.5 14.7188V13.5H8.5L8.46875 14.7188C8.46875 14.875 8.40625 15.125 8.3125 15.25L7.78125 16.0625C7.625 16.3125 7.25 16.5 6.9375 16.5H5.03125C4.71875 16.5 4.34375 16.3125 4.1875 16.0625L3.65625 15.25C3.53125 15.0938 3.5 14.9062 3.5 14.7188ZM6 0.5C9.03125 0.53125 11.5 2.96875 11.5 6C11.5 7.40625 10.9688 8.65625 10.125 9.59375C9.59375 10.1875 8.8125 11.4375 8.5 12.4688C8.5 12.4688 8.5 12.4688 8.5 12.5H3.46875C3.46875 12.4688 3.46875 12.4688 3.46875 12.4688C3.15625 11.4375 2.375 10.1875 1.84375 9.59375C1 8.625 0.5 7.375 0.5 6C0.5 3.09375 2.8125 0.53125 6 0.5ZM9 8.65625C9.625 7.90625 10 6.96875 10 6C10 3.8125 8.1875 2 5.96875 2C3.5 2.03125 2 4.0625 2 6C2 6.96875 2.34375 7.90625 2.96875 8.65625C3.46875 9.1875 4.0625 10.0938 4.5 11H7.46875C7.90625 10.0938 8.5 9.1875 9 8.65625ZM5.5 3C5.75 3 6 3.25 6 3.53125C6 3.78125 5.75 4 5.5 4C4.65625 4 4 4.6875 4 5.5C4 5.78125 3.75 6 3.5 6C3.21875 6 3 5.78125 3 5.5C3 4.125 4.09375 3 5.5 3Z" fill="#202223" />
                            </svg>
                            Pro Tip
                        </div>
                        <p className="mb-0">We highly advise leveraging user-generated content to enhance your e-commerce brand.</p>
                    </div>
                    <div className="d-flex align-items-center justify-content-between obCreateFeed_bottomAction">
                        <button className="btn obcreateFeed_bacnBtn" onClick={this.clearFeedTextOnBack}>
                            <svg xmlns="http://www.w3.org/2000/svg" width="14" height="11" viewBox="0 0 14 11" fill="none">
                                <path d="M13.2227 5.75C13.2227 6.24219 12.8398 6.625 12.375 6.625H3.98047L6.85156 9.52344C7.20703 9.85156 7.20703 10.4258 6.85156 10.7539C6.6875 10.918 6.46875 11 6.25 11C6.00391 11 5.78516 10.918 5.62109 10.7539L1.24609 6.37891C0.890625 6.05078 0.890625 5.47656 1.24609 5.14844L5.62109 0.773438C5.94922 0.417969 6.52344 0.417969 6.85156 0.773438C7.20703 1.10156 7.20703 1.67578 6.85156 2.00391L3.98047 4.875H12.375C12.8398 4.875 13.2227 5.28516 13.2227 5.75Z" fill="#6D7175" />
                            </svg>
                            Back
                        </button>
                        <button className={`btn btn-primary ${isLoading ? 'spinner spinner-white disabled spinner-right' : ''}`} disabled={isLoading} onClick={this.onAddFeed}>
                            Create Feed
                        </button>
                    </div>
                </div>
            </div>
        )
    }
}
