import moment from 'moment'
import React, { Component } from 'react'   
import DataNotFound from '../../analytics-new/elements/skeleton-loader-cards/notfound'
import { OverlayTrigger, Tooltip } from 'react-bootstrap'
import { Link } from 'react-router-dom'

export default class topProformingPost extends Component {
  render() {
    const {top_performing_post}= this.props
    return (
      <div className="dashboard_TopPosts">
        <div className="card db_TopPosts_popularChart db_TopPosts_chartBox mt-0">
          <div className="d-flex align-items-center justify-content-between db_TopPosts_cardHeading">
            <h4>Top Performing Posts  <OverlayTrigger placement='top' overlay={ <Tooltip>Your most engaging posts.</Tooltip> } >
                  <img src="https://cloud.taggbox.com/taggshop/app/assets/media/dashboard/icons/tooltip-icon.svg" alt="tooltip-icon" />
                  </OverlayTrigger> </h4>
            <div className="bs_box_icon">
               {top_performing_post?.length >0? 'CTA Clicks':''}
              </div>
          </div>
          {top_performing_post && top_performing_post.length ? top_performing_post.map((item,i)=>{
          return i < 4 && <Link to={`/analytic/${localStorage.getItem('wallId')}`} onClick={()=>localStorage.setItem('website_tab',1)}><div className="db_TopPosts_Contributors">
            <div clasName="card-body p-4">
              <div className="d-flex justify-content-between db_TopPosts_postOuter">
                <div className="db_topPostLeftAlign">
                  <div className="db_TopPosts_postImage">
                  <img src={item.postFileNew} alt=""  onError={event => event.target.src = `https://cloud.taggbox.com/analytics/media/svg/dashboard/no-image.svg`}/>  
                  </div>
                  <div className="d-flex flex-wrap flex-column db_TopPosts_authorMain">
                    <div className="d-flex align-items-center db_TopPosts_authorInner">
                      <div className="d-flex text-left flex-column db_TopPosts_authorDetail">
                        <h6 className="overflow-hidden text-nowrap text-ellipsis db_TopPosts_productTitle">
                          {item.name}
                        </h6>
                        <div className="d-flex align-items-center db_TopPosts_authordetailBottom">
                          <span className="db_TopPosts_authorHandle">@{item.username}</span>
                          <span className="db_TopPosts_dotSeprator">.</span> 
                          <span className="db_TopPosts_postDate">{moment(new Date(item.createdAt * 1000)).fromNow()}</span>
                        </div>
                      </div>
                    </div>
                  </div>
                </div> 
                <div className="db_topPost_rightAlign align-items-center"> 
                  <div className="db_topPost_SocialIcon">
                  <div className={`ts__icon ts-${item?.icon && item?.icon.includes('fa-') ? item?.icon.replace('fa-', '') :''}`}><div> </div></div>
                  </div>
                  </div> 
                <div className="db_topPost_rightAlign me-3"> <p className="overflow-hidden text-nowrap text-ellipsis db_TopPosts_productTitle">{item?.ctaClick} </p>
                
                </div>
              </div>
            </div>
          </div></Link>}) : 

          <div className="data-not-found-container">
            <div className="d-flex align-items-center justify-content-center flex-column text-center dataNotFound-inner">
              <h4>Curate Posts</h4>
              <p>Immersive content on your site can potentially improve visitors engagement and revenue.</p>
              <div className="not_conne_overlay d-flex flex-column flex-center">
                <Link className="btn btn-primary" to={`/content/moderation/${localStorage.getItem('wallId')}`}>
                Add Content
                </Link>
              </div>
            </div>
          </div>}
        </div>
      </div>
    )
  }
}
