import React, { Component } from 'react'
import { HOST_URL } from '../../../../actions/api'

export default class CaseStudy extends Component {
  render() {
    const {caseStudy} = this.props
    return (
        <div className="col-sm-12">
        <div className="d-flex justify-content-between align-items-center topCSM_section">
          <div className="topCSM_left">
              {caseStudy?.timeperiod ? <h2>{caseStudy?.timeperiod} {caseStudy?.username} 👋</h2> 
              :<p className="sk_line sk_line_height_20 sk_line_100 m-0 mb-5"></p>}
              <a  target="_blank" onClick={window.Intercom ? (e) =>{window.Intercom('show') }: ()=>{}} className="btn btn-outline me-3">Chat Now</a>
              <a  target="_blank" href={caseStudy?.book_call_link} className="btn btn-primary">Book A Call</a>
          </div>
          
          <div className="topCSM_right">
            <div className="topCSM_right_content">
            {caseStudy?.timeperiod ? <span className="topCSM_right_title">{caseStudy?.case_study_username}</span>  :<p className="sk_line sk_line_height_10 sk_line_40 m-0 mb-3"></p>}
            {caseStudy?.timeperiod ? <p className="topCSM_right_desc">{caseStudy?.case_study_description}</p>  :<><p className="sk_line sk_line_height_10 sk_line_100 m-0 mb-3"></p> <p className="sk_line sk_line_height_10 sk_line_100 m-0 mb-3"></p> <p className="sk_line sk_line_height_10 sk_line_100 m-0 mb-3"></p> <p className="sk_line sk_line_height_10 sk_line_100 m-0 mb-3"></p></>}
            {caseStudy?.timeperiod ? caseStudy?.embed_status ? <a href={caseStudy?.case_study_button_link} target="_blank" className="btn btn-outline">{caseStudy?.case_study_button_title}</a> :<a href={`${HOST_URL}website/publish/${localStorage.getItem('website_id')}`} className="btn btn-outline">Publish Now</a>:  <p className="sk_line sk_line_height_30 sk_line_40 m-0  mt-5"></p> }
            
            </div>
            <div className="topCSM_right_Image">
              <img src="https://cloud.taggbox.com/taggshop/app/assets/media/dashboard/csm.png" alt="Image" />
            </div>
          </div>
        </div>
      </div>
    )
  }
}
