import React, { Component } from 'react'
import { Tab, Tabs } from 'react-bootstrap'
import AddNewProductImport from './modal/addNewProductImport.js'
import { CONNECT_SHOPIFY_STORE } from '../../../../actions/productAction.js'
import Swal from 'sweetalert2'
import withRouter from '../../../routes/WithRouter.js'

class SyncProduct extends Component {
    state={uploadCsv: false,shopifyUrl: '', shopify_error: false}
    synctab = (name, icon) => {
        return <p><img src={`https://cloud.taggbox.com/taggshop/app/assets/media/onboarding-icons/${icon}.svg`} /> {name}</p>
    }

    onMoreTab = () => {
        const { updateExtraData } = this.props
        Swal.fire({
          title: "You have to connect your Account to use more options",
          icon: "info",
          showCancelButton: true,
          confirmButtonText: "I'll do it later",
          cancelButtonText:"Proceed"
        }).then((result) => {
          if (result.isDismissed && result.dismiss == "cancel") {
            updateExtraData({SKIP: true})
            this.props.navigate(`/content/products`)
          }
        });
      }

    render() {
        const { uploadCsv, shopifyUrl, shopify_error } = this.state
        return (
            <div className="syncProduct_wrap">
                <div className="obSyncProduct_container">
                    <div className="obSyncProduct_heading">
                        <div className="obSyncleftAlign">
                            <h4>Sync Product</h4>
                            <p>You can select your preferred platform to sync your Products.</p>
                        </div>
                        <div className="obImport_btn" onClick={()=>this.setState({uploadCsv: true})} style={{cursor: 'pointer'}}>
                            <svg xmlns="http://www.w3.org/2000/svg" width="10" height="12" viewBox="0 0 10 12" fill="none">
                                <path d="M7.54102 3.94141C7.8457 3.61133 8.37891 3.61133 8.68359 3.94141C9.01367 4.24609 9.01367 4.7793 8.68359 5.08398L5.43359 8.33398C5.12891 8.66406 4.5957 8.66406 4.29102 8.33398L1.04102 5.08398C0.710938 4.7793 0.710938 4.24609 1.04102 3.94141C1.19336 3.78906 1.39648 3.6875 1.625 3.6875C1.82812 3.6875 2.03125 3.78906 2.18359 3.94141L4.0625 5.79492V1.25C4.0625 0.818359 4.41797 0.4375 4.875 0.4375C5.30664 0.4375 5.6875 0.818359 5.6875 1.25V5.79492L7.54102 3.94141ZM8.9375 10.1875C9.36914 10.1875 9.75 10.5684 9.75 11C9.75 11.457 9.36914 11.8125 8.9375 11.8125H0.8125C0.355469 11.8125 0 11.457 0 11C0 10.5684 0.355469 10.1875 0.8125 10.1875H8.9375Z" fill="#4E69ED" />
                            </svg>
                            Import Products List
                        </div>
                    </div>
                    <div className="obSyncProduct_tabsContainer">
                        <Tabs defaultActiveKey="shopify" className="" onSelect={e => {if(e == "more") this.onMoreTab()}}>
                            <Tab eventKey="shopify" title={this.synctab("Shopify", "obShopify-icon")} className="postType">
                                <div className="obFormGroup-wrap">
                                    <div className="form-group">
                                        <input required="" className="form-control urlshop" placeholder="example.myshopify.com" id="editurl" type="text" value={shopifyUrl} onChange={(e) => this.setState({ shopifyUrl: e.target.value, shopify_error: false })}  />
                                        {shopify_error ? <span className="errorMessage"> This field is required </span> : <></>}
                                    </div>
                                    <button type="submit" className="btn shopify_btn" value="shopify" onClick={() => {if(shopifyUrl){localStorage.setItem("onboarding", true);CONNECT_SHOPIFY_STORE(shopifyUrl)} else this.setState({shopify_error: true})}}>
                                        <span>
                                            <img src="https://cloud.taggbox.com/taggshop/app/assets/media/display-logo/shopify.svg" alt="shopify-icon" />
                                        </span>
                                        Connect Shop
                                    </button>
                                </div>
                                <div className="obSyncProduct_tooltip">
                                    <div className="obInfobox">
                                        <svg xmlns="http://www.w3.org/2000/svg" width="16" height="17" viewBox="0 0 16 17" fill="none">
                                            <path d="M8 0.5C12.4062 0.5 16 4.09375 16 8.5C16 12.9375 12.4062 16.5 8 16.5C3.5625 16.5 0 12.9375 0 8.5C0 4.09375 3.5625 0.5 8 0.5ZM8 15C11.5625 15 14.5 12.0938 14.5 8.5C14.5 4.9375 11.5625 2 8 2C4.40625 2 1.5 4.9375 1.5 8.5C1.5 12.0938 4.40625 15 8 15ZM9.25 11C9.65625 11 10 11.3438 10 11.75C10 12.1875 9.65625 12.5 9.25 12.5H6.75C6.3125 12.5 6 12.1875 6 11.75C6 11.3438 6.3125 11 6.75 11H7.25V9H7C6.5625 9 6.25 8.6875 6.25 8.25C6.25 7.84375 6.5625 7.5 7 7.5H8C8.40625 7.5 8.75 7.84375 8.75 8.25V11H9.25ZM8 6.5C7.4375 6.5 7 6.0625 7 5.5C7 4.96875 7.4375 4.5 8 4.5C8.53125 4.5 9 4.96875 9 5.5C9 6.0625 8.53125 6.5 8 6.5Z" fill="#202223" />
                                        </svg>
                                        Info
                                    </div>
                                    <p className="mb-0">Tagshop only sync 500 products at a time, please reach out to <a href="">support@tagshop.ai</a> for any query</p>
                                </div>
                            </Tab>
    
                            <Tab eventKey="more" title={'More'} className="postType" onClick={this.props.onMoreTab}>
                                
                            </Tab>
                        </Tabs>
                    </div>
                </div>
                {uploadCsv ? <AddNewProductImport show={uploadCsv} onHide={() => this.setState({ uploadCsv: false })} countAllPage={this.refreshProducts} fromOB={true} navigate={this.props.navigate}/> : null}
            </div>
        )
    }
}

export default withRouter(SyncProduct)