import React, { Component } from 'react' 
import DataNotFound from '../../analytics-new/elements/skeleton-loader-cards/notfound'
import { kFormatter } from '../../../../utils'
import { OverlayTrigger, Tooltip } from 'react-bootstrap'
import { Link } from 'react-router-dom'
export default class popularProducts extends Component {
  render() {
    const {top_performing_product}= this.props
    return (
      <div className="dashboard_TopPosts dashboard_TopProducts">
        <div className="card db_TopPosts_popularChart db_TopPosts_chartBox mt-0">
          <div className="d-flex align-items-center justify-content-between db_TopPosts_cardHeading">
            <h4>Top Performing Products <OverlayTrigger placement='top' overlay={ <Tooltip>Now that you know your best sellers, create product  bundles to improve AOV!.</Tooltip> } >
                  <img src="https://cloud.taggbox.com/taggshop/app/assets/media/dashboard/icons/tooltip-icon.svg" alt="tooltip-icon" />
                  </OverlayTrigger> </h4>
            <div className="bs_box_icon">
           {top_performing_product?.length > 0 ? 'Assisted Revenue' :''}
              </div>
          </div>
          {top_performing_product && top_performing_product.length ? top_performing_product.map((item,i)=>{
          return i < 4 && <Link to={`/analytic/${localStorage.getItem('wallId')}`} onClick={()=>localStorage.setItem('conversion_tab',1)}> <div className="db_TopPosts_Contributors">
            <div clasName="card-body p-4">
              <div className="d-flex justify-content-between db_TopPosts_postOuter">
                <div className="db_topPostLeftAlign">
                  <div className="db_TopPosts_postImage">
                  <img src={item.product_image} alt=""  onError={event => event.target.src = `https://cloud.taggbox.com/analytics/media/svg/dashboard/no-image.svg`}/>
                  </div>
                  <div className="d-flex flex-wrap flex-column db_TopPosts_authorMain">
                    <div className="d-flex align-items-center db_TopPosts_authorInner">
                      <div className="d-flex text-left flex-column db_TopPosts_authorDetail">
                        <h6 className="overflow-hidden text-nowrap text-ellipsis db_TopPosts_productTitle">
                        {item.product_title}
                        </h6>
                        <div className="d-flex align-items-center db_TopPosts_authordetailBottom">
                          <span className="db_TopPosts_productSKU">{item.product_sku}</span>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
                <div className="db_topPost_rightAlign">
                    <span className="db_topPosstProduct_price me-3">
                      {item.discount_currency_symbol}{kFormatter(item.assisted)}
                    </span>
                </div>
              </div>
            </div>
          </div></Link>}):
          <div className="data-not-found-container">
            <div className="d-flex align-items-center justify-content-center flex-column text-center dataNotFound-inner">
              <h4 className='card-title mb-2'> Manage Product Catalog </h4>
              <p>Start tagging products to know which of your products are shoppers' top-picks!.</p>
              <div className="not_conne_overlay d-flex flex-row flex-center">
              <Link className="btn btn-outline mx-2" to={`/content/moderation/${localStorage.getItem('wallId')}`}>
                  Tag Products
                </Link> <Link className="btn btn-primary mx-2" to={`/content/products`}>
                Sync Product
                </Link>
              </div>
            </div>
          </div>}
        </div>
      </div>
    )
  }
}
