import React, { Component } from 'react'
import Dropdown from 'react-bootstrap/Dropdown';
import DropdownButton from 'react-bootstrap/DropdownButton';
import { connect } from 'react-redux';
import { DASHBOARD_SHOPON_ANALYTICS, DASHBOARD_WEBSITE_ANALYTICS } from '../../../../actions/sceneActions';
import { kFormatter } from '../../../../utils';
import withRouter from '../../../routes/WithRouter'; 
import { OverlayTrigger, Tooltip } from 'react-bootstrap';
import { UPDATE_EXTRA_DATA } from '../../../../actions/initActions';
import { CHANGE_SHOPON_WALL } from '../../../../actions/shoponAction';
import moment from 'moment';

 class TabShopOn extends Component {
    state = {   activeShopOn: [] }

    componentDidMount() { 
        if (localStorage.getItem('shopOn_id')) {   
            var gallery = this.props.shopOnData?.shopOnList.find((item) => item.id == localStorage.getItem('shopOn_id'));  
            this.setState({ activeShopOn: gallery }) 
        } else { 
            let firstWallId = this.props.shopOnData?.shopOnList && this.props.shopOnData?.shopOnList.length ? this.props.shopOnData?.shopOnList[0] : ''  
            this.setState({ activeShopOn: firstWallId })
        } 
    }

     
  componentDidUpdate(prevProps, prevState) {
    if (this.props.shopOnData?.shopOnList !== prevProps.shopOnData?.shopOnList) { 
      if (localStorage.getItem('shopOn_id')) {   
          var gallery = this.props.shopOnData?.shopOnList.find((item) => item.id == localStorage.getItem('shopOn_id'));  
          this.setState({ activeShopOn: gallery }) 
      } else { 
          let firstWallId = this.props.shopOnData?.shopOnList && this.props.shopOnData?.shopOnList.length ? this.props.shopOnData?.shopOnList[0] : ''  
          this.setState({ activeShopOn: firstWallId })
      } 
    }
  }


  viewDetails = (id) => {
    const { shopOnData, allWalls } = this.props
    UPDATE_EXTRA_DATA({ shopOn_id: id }).then(res => {
        localStorage.setItem('shopOn_id', id); 
        var webData = shopOnData?.shopOnList?.find((x) => x.id == id); 
        var newwallID = webData?.wallId ? webData?.wallId : allWalls[0].id;
        localStorage.setItem('shopOnActive_wall', newwallID)
     //  this.props.CHANGE_SHOPON_WALL(newwallID, 21);
        setTimeout(() => {
          this.props.navigate(`/shoponBio/preview/${id}`)
        }, 100);

    })
}
 

    ChangeGallery = (e) => { 
        const { shopOnData ,start, end } = this.props
        let startDate = moment(start,"DD-MM-YYYY").startOf('day').format("YYYY-MM-DD HH:mm:ss");
        let endDate = moment(end, "DD-MM-YYYY").endOf('day').format("YYYY-MM-DD HH:mm:ss");
        var gallery = shopOnData?.shopOnList.find((item) => item.id == e.id);
        this.setState({ activeShopOn: gallery })
        this.props.DASHBOARD_SHOPON_ANALYTICS(e.id, startDate, endDate)
    }


  render() {
    const {activeShopOn} = this.state
    const {shopOnData, homeData} = this.props  
    return (
      <>
       <div className="d-flex align-items-center justify-content-end mp_veiwDetails">
                          <button className="viewDetails"  onClick={()=>this.viewDetails(activeShopOn?.id)}>view details</button>
                        <DropdownButton id="dropdown-basic-button" title={activeShopOn?.name}>
                         {this.props.shopOnData?.shopOnList?.map((item, i) => {
                          return  <Dropdown.Item onClick={()=>this.ChangeGallery(item)}>{item?.name}</Dropdown.Item>}) 
                         }
                        </DropdownButton>
                      </div>
                      <div className="col-sm-12 custom-padding">
                        <div className="row">
                         
                        {homeData?.shopon?.series?.map((item , i)=>{
                            return <div className="col-sm-6 col-md-6 col-lg-3 custom-padding" key={i}>
                                      <div className="d-flex flex-wrap flex-column bs_box_outer">
                                        <div className="d-flex align-items-center justify-content-between box_top">
                                          <span className="bs_box_title">{item.name}
                                            
                                          </span>
                                          <div className="bs_box_icon">
                                            <OverlayTrigger placement='top' overlay={ <Tooltip> {item?.tool_tip}</Tooltip> } >
                                              <img src="https://cloud.taggbox.com/taggshop/app/assets/media/dashboard/icons/tooltip-icon.svg" alt="tooltip-icon" />
                                            </OverlayTrigger> 
                                          </div>
                                        </div>
                                      <div className="d-flex align-items-center justify-content-start box_bottom"> 
                                      {homeData.shopon_loader ? <p className="sk_line sk_line_height_10 sk_line_20 m-0 mb-5"></p> : <span className="bs_id_value"> {kFormatter(item.total) }</span>}
                                      </div>
                                    </div>
                                  </div>
                          })}
                          

                        </div>
                      </div> 
                      </>
    )
  }
}

const mapStateToProps = state => {
    return {
      homeData: state.dashboardData,
      shopOnData: state.shopOnList,
      allWalls: state.dashboardData?.dashboarddata?.allWalls,
    }
  }
  
  const mapDispatchToProps = dispatch => {
    return { 
      DASHBOARD_SHOPON_ANALYTICS: (id, startDate, endDate) => dispatch(DASHBOARD_SHOPON_ANALYTICS(id, startDate, endDate)),
      CHANGE_SHOPON_WALL: (wallId, limit) => dispatch(CHANGE_SHOPON_WALL(wallId, limit)),
    }
  }
  
  export default connect(mapStateToProps, mapDispatchToProps)(withRouter(TabShopOn));