import React, { Component } from "react";
import { StepHome } from "../components/stepHome/index-new";
import HomeDashboard from "../components/home";
import { connect } from 'react-redux';
import { TAGGBOX_USER_HOME_DATA } from "../../actions/sceneActions";
import LoadingScreen from "./LoadingScreen";
import DashboardSocialFeeds from "../components/home/elements/LoadingView";
import { getUserInfo_afterChanges } from "../../actions/loginActions";
import { UPDATE_EXTRA_DATA } from "../../actions/initActions";
import OnBoarding from "../components/OnBoarding";

class DashboardPage extends Component {
  componentDidMount(){ 
    //  this.updateExtraData({FEED_CREATED:true, PRODUCT_ADDED: true, APPS_MORE_USED: true , WEBSITE_VISITED: true , FEED_CUSTOMIZED: true, CUSTOMIZATION_SAVED: true, EMAIL_VISITED: true, EMAIL_PUBLISHED: true, EMAIL_PLATFORM_SELECTED: true, SHOPON_VISITED: true, SHOPON_BIO_ADDED: true, SHOPON_LINK_COPIED: true })
    setTimeout(() => {
      this.checkForFirstTime()
    }, 1000);
  }

  checkForFirstTime = () => {
    const { authorized } = this.props;
    if (authorized?.userData?.user_data.user.created) {
      const currentDate = new Date('2024-03-29T04:57:49.040Z').getTime();
      const createdDate = new Date(new Date(authorized?.userData?.user_data.user.created).toISOString()).getTime();
      // console.log('currentDate, currentDate', currentDate, createdDate, authorized?.userData?.user_data.user.created)
      let homeData = authorized?.userData?.user_data.user.extra_data ? JSON.parse(authorized?.userData?.user_data.user.extra_data) : {};
      if (createdDate < currentDate) {
        // console.log("Old User");
        if (!homeData.PRODUCT_TAGGED_POST) {
          UPDATE_EXTRA_DATA({ PRODUCT_TAGGED_POST: true }).then(res => { this.props.getUserInfo_afterChanges() })
        }
      } else {
        // console.log("New User");
      }
    }
  }

  checkIfCompleted = () => {
    const { authorized } = this.props;
    let homeData = authorized?.userData?.user_data.user.extra_data ? JSON.parse(authorized?.userData?.user_data.user.extra_data) : {};
    var completed = true
    if (!homeData.FEED_CREATED) completed = false
    if (!homeData.CHOOSE_NETWORK) completed = false
    if (!homeData.PRODUCT_ADDED) completed = false
    if (homeData.SKIP) completed = true
    // if (!homeData.APPS_MORE_USED) completed = false
    // if (!homeData.WEBSITE_VISITED) completed = false
    // if (!homeData.FEED_CUSTOMIZED) completed = false
    // if (!homeData.CUSTOMIZATION_SAVED) completed = false
    // if (!homeData.EMAIL_VISITED) completed = false
    // if (!homeData.EMAIL_PUBLISHED) completed = false
    // if (!homeData.EMAIL_PLATFORM_SELECTED) completed = false
    // if (!homeData.SHOPON_VISITED) completed = false
    // if (!homeData.SHOPON_BIO_ADDED) completed = false
    // if (!homeData.SHOPON_LINK_COPIED) completed = false
    return completed;
  }

  checkIfStepsCompleted = () => {
    const { authorized } = this.props;
    let homeData = authorized?.userData?.user_data.user.extra_data ? JSON.parse(authorized?.userData?.user_data.user.extra_data) : {};
    var completed = true
    if (!homeData.FEED_CREATED) completed = false
    // if (!homeData.CHOOSE_NETWORK) completed = false
    if (!homeData.PRODUCT_ADDED) completed = false
    if (!homeData.APPS_MORE_USED) completed = false
    if (!homeData.WEBSITE_VISITED) completed = false
    if (!homeData.FEED_CUSTOMIZED) completed = false
    if (!homeData.CUSTOMIZATION_SAVED) completed = false
    if (!homeData.EMAIL_VISITED) completed = false
    if (!homeData.EMAIL_PUBLISHED) completed = false
    if (!homeData.EMAIL_PLATFORM_SELECTED) completed = false
    if (!homeData.SHOPON_VISITED) completed = false
    if (!homeData.SHOPON_BIO_ADDED) completed = false
    if (!homeData.SHOPON_LINK_COPIED) completed = false

    return completed;
  }

    updateExtraData = (obj) => {
      const { authorized } = this.props;
      let homeData = authorized?.userData?.user_data.user.extra_data ? JSON.parse(authorized?.userData?.user_data.user.extra_data) : {};
    if (!(this.checkIfCompleted())) {
        UPDATE_EXTRA_DATA(obj).then(res => {this.props.getUserInfo_afterChanges() })
    }
}

  render() {
    const { homeData , authorized} = this.props; 
    let homeStepData = authorized?.userData?.user_data.user.extra_data ? JSON.parse(authorized?.userData?.user_data.user.extra_data):{}; 
    let login_number =  authorized?.userData?.user_data?.user?.login_number 

    return <div className="d-flex flex-column-fluid main_content no_subheader align-items-center">
             {authorized?.userData?.user_data ? this.checkIfCompleted() ? this.checkIfStepsCompleted() ? <HomeDashboard homeData={homeData} /> : login_number > 3 ?  <HomeDashboard homeData={homeData} />: <StepHome homeData={homeStepData} />: <OnBoarding updateExtraData={this.updateExtraData} homeData={homeStepData}/> : <></>}
             {/* <OnBoarding updateExtraData={this.updateExtraData}/> */}
           </div>
  }
}

const mapStateToProps = state => {
  return {
    homeData: state.dashboardData.dashboarddata,
    authorized: state.authorized,
  }
}


const mapDispatchToProps = dispatch => {
  return {
    // TAGGBOX_USER_HOME_DATA: () => dispatch(TAGGBOX_USER_HOME_DATA()),
     getUserInfo_afterChanges: () => dispatch(getUserInfo_afterChanges()),
  }
} 

export default connect(mapStateToProps, mapDispatchToProps)(DashboardPage);
