import React, { Component } from 'react' 
import parse from 'html-react-parser';
import { CLOUDPATH, CLOUDPATH_TAGGSHOP, HOST_URL_TAGGSHOP } from '../../../../actions/api';
import { Link } from 'react-router-dom';
import { showAddAccounntAlert } from '../../../../utils';


export default class DashboardSocialAccount extends Component {
    NETWOEK_ICON_Name = (name) => {
        return name.toLowerCase().replaceAll(" ", "-").replace('google-review', 'google');
    }

    reconnectAccount = (obj) => {
        const item = {
            id: obj.networkId,
            name: (obj.networkName).toLowerCase()
        }
        // localStorage.setItem("feedId", obj.id)
        showAddAccounntAlert(item, true, false)
    }
    
    connectAccount = (obj) => {
        const item = {
            id: obj.networkId,
            name: (obj.networkName).toLowerCase()
        }
        localStorage.setItem("feedId", obj.id)
        showAddAccounntAlert(item, false, false)
    }


    render() {
        const { allAccounts, feeds } = this.props;
        return (
            <div className="col-md-6 col-lg-6 col-xl-6 home-connected-account">
                <div className="card card-custom gutter-b connect_account">
                    <div className="card-body">
                        <div className="d-flex align-items-center justify-content-between mb-3">
                            <h4 className="card-title mb-2">Social Accounts</h4>
                            <Link to={`/transactions#connectedAccounts`} className="btn text-primary p-0">View all</Link>
                        </div>
                        <div className="pr-4 dashboard_socialAccounts" >
                            <div className="h-100">
                                {allAccounts !== null && allAccounts && allAccounts.length > 0 ?
                                    <>  {feeds !== null && feeds ? <>

                                        {feeds.map((feeddata, index) => {
                                            return <div className="row align-items-center pt-2 pb-2 users_row" key={index}>
                                                <div className="d-flex align-items-center flex-grow-1 w-50 px-3">
                                                    <div className="symbol symbol-25 symbol-light mr-3 con_user_img ml-1">
                                                        <img className="img-fluid mCS_img_loaded" src={`${CLOUDPATH_TAGGSHOP}/app/assets/media/square-icons/${this.NETWOEK_ICON_Name(feeddata.Network.name).toLowerCase()}.svg?ver=1`} />
                                                    </div>

                                                    <div className="d-flex text-left flex-column conn_user_info w-50">
                                                        <p title="From Hashtag(#)" className="text-dark m-0 text-nowrap d-flex align-items-center">
                                                            <strong className="username_conn text-nowrap overflow-hidden d-block text-ellipsis">
                                                                {parse(feeddata.Feed.feedName)}
                                                                {feeddata.Feed.socialLink ? <a href={feeddata.Feed.socialLink} target="_blank">
                                                                    <i className="fas fa-external-link-alt icon-nm ml-1"></i>
                                                                </a> : null}
                                                            </strong>

                                                            {/*  */}
                                                        </p>
                                                    </div>
                                                </div>

                                                <div className=" w-50 text-right pr-3">
                                                    <span className={`reconnect${feeddata.Feed.id}`}>
                                                        <div className=" btn btn-outline-primary btn-xs" onClick={() => this.reconnectAccount({id: feeddata.Feed.id, networkId: feeddata.Network.id, networkName: feeddata.Network.name})}> Reconnect</div></span>
                                                </div>
                                            </div>
                                        })}
                                    </>
                                        : ''

                                    }

                                        {allAccounts.map((item, index) => {
                                            return <div className="d-flex align-items-center pt-2 pb-2 users_row" key={index}>
                                                <div className="d-flex align-items-center flex-grow-1 w-50">
                                                    <div className="symbol symbol-25 symbol-light mr-6 con_user_img ml-1">
                                                        <span style={{ backgroundImage: `url('${item.ConnectedAccounts.profilePicture == "" || item.ConnectedAccounts.profilePicture == "https:" || item.ConnectedAccounts.profilePicture == null ? `https://ui-avatars.com/api/?name=${item.ConnectedAccounts.accountName}&background=ECF1F4&color=AC93F2&length=1&length=1` : item.ConnectedAccounts.profilePicture}')` }}></span>
                                                        <i>
                                                            <img alt="Pic" src={`${CLOUDPATH_TAGGSHOP}/app/assets/media/square-icons/${this.NETWOEK_ICON_Name(item.Network.name).toLowerCase()}.svg?var=1`} />
                                                        </i>
                                                    </div>
                                                    <div className="d-flex text-left flex-column conn_user_info w-75">
                                                        <h5 className="text-dark-75 text-nowrap overflow-hidden d-block text-ellipsis">{item.ConnectedAccounts.accountName}</h5>
                                                        <small className="text-nowrap overflow-hidden d-block text-ellipsis">@{item.ConnectedAccounts.accountName}</small>
                                                    </div>
                                                </div>
                                                {item.ConnectedAccounts.api == 2 ?
                                                    <div className="conn_status connnected text-right">
                                                        <div className="conn_status_wrap">
                                                            <span className="conn_icon">
                                                                <img className="img-fluid" src={`${CLOUDPATH_TAGGSHOP}/app/assets/media/square-icons/connected.svg`} />
                                                            </span>
                                                            <span className="font-weight-bolder acc_status">Connected</span>
                                                        </div>
                                                        <small>{item.Network.name} Profile</small>
                                                    </div>
                                                    : <div className="conn_status disconnnected text-right">
                                                        <div className="conn_status_wrap">
                                                                <a href="javascript:(0)" className="btn btn-outline-primary btn-xs " onClick={() => this.reconnectAccount({networkId: item.Network.id, networkName: item.Network.name})}>Reconnect</a>
                                                        </div>
                                                        <small>{item.Network.name} Profile</small>
                                                    </div>}

                                            </div>
                                        })
                                        }





                                    </> :
                                   


                                    <div className="data-not-found-container dashboard_socialAccounts_empty">
                                        <div className="d-flex align-items-center justify-content-center flex-column text-center dataNotFound-inner">
                                        <h4 className='card-title mb-2'> Add your Social Accounts </h4>
                                        <p>Connecting your social media platform is the first step.</p>
                                        <div className="not_conne_overlay d-flex flex-row flex-center">
                                        <Link to={`/transactions#connectedAccounts`} className="btn btn-primary">Connect</Link>
                                        </div>
                                        </div>
                                    </div>}





                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
