import React, { Component } from 'react'
import {  Modal } from 'react-bootstrap';
import { CLOUDPATH_TAGGSHOP, HOST_URL_TAGGSHOP } from '../../../../actions/api' 
import ConectFeedPopup from './conectFeedPopup';
import withRouter from '../../../routes/WithRouter';
class AddfeedPopup extends Component {
    state = {
        openPopup: true,
        connectFeedpopup:false,
        connectFeedId:0,
    }

    closePopup = event => {
        const { openPopup } = this.state;
        this.setState({ openPopup: false})
    }
    closePopup = event => {
        const { openPopup } = this.state;
        this.setState({ openPopup: false})
    }
    openConnectPopup=(id)=>{
        this.setState({ connectFeedId: id, connectFeedpopup:true})
    }
    closeConnectPopup=event =>{
        this.setState({connectFeedpopup:false, openPopup: false})
    }
    openModeration=event=>{
        window.location.replace(`${HOST_URL_TAGGSHOP}/content/moderation/${localStorage.getItem('wallId')}`)
    }

    openAddfeed = () => {
        this.props.openAddfeed()
        this.closePopup()
    }

    render() {
        const {openPopup , connectFeedpopup, connectFeedId} = this.state

        return (<>
            <Modal size="md" dialogClassName="modal-dialog-centered AddFeedModal_Outer" show={openPopup} onHide={this.closePopup}>
                <Modal.Header closeButton className="border-0 px-4 py-4">
                    <Modal.Title></Modal.Title>
                </Modal.Header>
                <Modal.Body className="pt-0">
                    <h2 className="text-center mb-5 font-weight-boldest">Pick A Source</h2>
                    <ul className="networks_AFM">
                        <li className="instagram-business">
                            <button className="AddFeedModal" onClick={()=> this.setState({ connectFeedId: 18, connectFeedpopup:true, openPopup:false})}>
                                <img src={`${CLOUDPATH_TAGGSHOP}/assets/media/square-icons/instagram-business.svg`} alt="Instagram" className="img-fluid"/>
                                <span>Instagram</span>
                            </button>
                        </li>
                        <li className="other">
                            <button className="AddFeedModal otherFeed" onClick={this.openAddfeed}>
                                <img src={`${CLOUDPATH_TAGGSHOP}/assets/media/square-icons/other.svg`} alt="TikTok" className="img-fluid"/>
                                <span>Other</span>
                            </button>
                        </li>
                    </ul>
                </Modal.Body>
            </Modal>
             {connectFeedpopup ? <ConectFeedPopup closeConnectPopup={this.closeConnectPopup} connectFeedId={connectFeedId} navigate={this.props.navigate} />:''} 
            </>
        )
    }
}

 
export default withRouter(AddfeedPopup)