import React, { Component } from 'react'
import Dropdown from 'react-bootstrap/Dropdown';
import DropdownButton from 'react-bootstrap/DropdownButton';
import { connect } from 'react-redux';
import { DASHBOARD_WEBSITE_ANALYTICS } from '../../../../actions/sceneActions';
import { kFormatter } from '../../../../utils';
import withRouter from '../../../routes/WithRouter'; 
import { OverlayTrigger, Tooltip } from 'react-bootstrap';
import { UPDATE_EXTRA_DATA } from '../../../../actions/initActions';
import { CHANGE_WEBSITE, CHANGE_WEBSITE_WALL } from '../../../../actions/themeActions/themeActions';
import { ACTIVE_WEBSITE_ID_REDUCER } from '../../../../reducer/keys';
import moment from 'moment';

 class TabWebsite extends Component {
    state = {   activeGallery: [] }

    componentDidMount() { 
        if (localStorage.getItem('website_id')) {   
            var gallery = this.props.websiteData?.websiteList.find((x) => x.wall?.Wall?.id == localStorage.getItem('website_id')); 
            this.setState({ activeGallery: gallery }) 
        } else { 
            let firstWallId = this.props.websiteData?.websiteList && this.props.websiteData?.websiteList.length ? this.props.websiteData?.websiteList[0] : ''  
            this.setState({ activeGallery: firstWallId })
        } 
    }

    componentDidUpdate(prevProps, prevState) {
      if (this.props.websiteData?.websiteList !== prevProps.websiteData?.websiteList) { 
        if (localStorage.getItem('website_id')) {   
            var gallery = this.props.websiteData?.websiteList.find((x) => x.wall?.Wall?.id == localStorage.getItem('website_id')); 
            this.setState({ activeGallery: gallery }) 
        } else { 
            let firstWallId = this.props.websiteData?.websiteList && this.props.websiteData?.websiteList.length ? this.props.websiteData?.websiteList[0] : ''  
            this.setState({ activeGallery: firstWallId })
        } 
      }
    }

 
   
     viewDetails=(id)=>{
      const {   websiteData  } = this.props   
      UPDATE_EXTRA_DATA({website_id:id}).then(res => {
        localStorage.setItem('website_id',id);  
        var webData = websiteData?.websiteList?.find((x)=> x.wall.Wall.id == id);
        this.props.GET_APP_DATA(webData);
        this.props.ACTIVE_WEBSITE_ID();

        this.props.CHANGE_WEBSITE_WALL(webData?.wall?.Wall?.wallId ? webData?.wall?.Wall?.wallId : localStorage.getItem('active_wall'), 20, webData?.wall?.ThemeRule?.themeId);
        setTimeout(() => {
          this.props.navigate(`/website/publish/${id}`)
        }, 100);
      })
  }

     
    ChangeGallery = (e) => { 
        const { websiteData, start, end } = this.props
        let startDate = moment(start,"DD-MM-YYYY").startOf('day').format("YYYY-MM-DD HH:mm:ss");
        let endDate = moment(end, "DD-MM-YYYY").endOf('day').format("YYYY-MM-DD HH:mm:ss");
        var gallery = websiteData?.websiteList.find((item) => item.wall?.Wall?.id == e.id); 
        this.setState({ activeGallery: gallery })
        this.props.DASHBOARD_WEBSITE_ANALYTICS(e.id, startDate, endDate)
    }

    
  render() {
    const {activeGallery} = this.state 
    const {websiteData, homeData} = this.props 
    return (
      <>
       <div className="d-flex align-items-center justify-content-end mp_veiwDetails">
                     <button className="viewDetails"  onClick={()=>this.viewDetails(activeGallery?.wall?.Wall?.id)}>view details</button>
                        <DropdownButton id="dropdown-basic-button" title={activeGallery?.wall?.Wall?.name}>
                         {this.props.websiteData?.websiteList?.map((item, i) => {
                          return  <Dropdown.Item onClick={()=>this.ChangeGallery(item.wall?.Wall)}>{item.wall?.Wall?.name}</Dropdown.Item>})
                         }
                        </DropdownButton>
                      </div>
                      <div className="col-sm-12 custom-padding">
                        <div className="row">
                       
                        {homeData?.website?.series?.map((item , i)=>{
                            return <div className="col-sm-6 col-md-6 col-lg-3 custom-padding" key={i}>
                                      <div className="d-flex flex-wrap flex-column bs_box_outer">
                                        <div className="d-flex align-items-center justify-content-between box_top">
                                          <span className="bs_box_title">{item.name}
                                            
                                          </span>
                                          <div className="bs_box_icon">
                                            <OverlayTrigger placement='top' overlay={ <Tooltip> {item?.tool_tip}</Tooltip> } >
                                              <img src="https://cloud.taggbox.com/taggshop/app/assets/media/dashboard/icons/tooltip-icon.svg" alt="tooltip-icon" />
                                            </OverlayTrigger> 
                                          </div>
                                        </div>
                                      <div className="d-flex align-items-center justify-content-start box_bottom">
                                      {homeData.website_loader ? <p className="sk_line sk_line_height_10 sk_line_20 m-0 mb-5"></p> :<span className="bs_id_value"> {kFormatter((item.total))} </span> }
                                      </div>
                                    </div>
                                  </div>
                          })}
                          
                        </div>
                      </div> 
                      </>
    )
  }
}

const mapStateToProps = state => {
    return {
      homeData: state.dashboardData,
      websiteData: state.websiteList,
    }
  }
  
  const mapDispatchToProps = dispatch => {

    return { 
      DASHBOARD_WEBSITE_ANALYTICS: (id, startDate, endDate) => dispatch(DASHBOARD_WEBSITE_ANALYTICS(id, startDate, endDate)),
      GET_APP_DATA: (data) => dispatch(CHANGE_WEBSITE(data)), 
      ACTIVE_WEBSITE_ID:()=> dispatch({ type: ACTIVE_WEBSITE_ID_REDUCER, payload: localStorage.getItem('website_id') }),
      CHANGE_WEBSITE_WALL: (wallId, limit, themeId) => dispatch(CHANGE_WEBSITE_WALL(wallId, limit, themeId)), 
    }
  }
  
  export default connect(mapStateToProps, mapDispatchToProps)(withRouter(TabWebsite));