import React, { Component } from 'react'
import { connect } from 'react-redux';
import { togglePriceSection } from '../../../../actions/show_modal_Action';
import { OverlayTrigger, Tooltip } from 'react-bootstrap';


 class DashboardSocialFeeds extends Component {

    render() {
        const { allfeedsCount, authorized } = this.props;
        let widthProgress = parseInt((allfeedsCount / (authorized && authorized.userData && authorized.userData.user_data ? authorized.userData.user_data.UserRule.feeds : 100)) * 100);
        return (
                <div className="card card-custom card-stretch gutter-b home-feed-section">
                    <div className="wall_progress card-body">
                        <div className="d-flex align-items-center justify-content-between">
                        <h4 className="card-title">Feed count</h4>
                            <OverlayTrigger placement='top' overlay={ <Tooltip> No. of feeds created vs the total no. of feeds available in your plan. </Tooltip> } >
                                    <img src="https://cloud.taggbox.com/taggshop/app/assets/media/dashboard/icons/tooltip-icon.svg" alt="tooltip-icon" />
                             </OverlayTrigger>
                            {/* {UpgradeNow == 1 ? <span className="btn text-primary p-0" id="showpriceFn" onClick={()=>{this.props.dispatch(togglePriceSection(true, 2))}} >Upgrade Now</span> : ""} */}
                        </div>
                        <h3>{allfeedsCount}/{authorized?.userData?.user_data?.UserRule?.feeds}</h3>
                        <div className="progress_range">
                            <div className="progress bar_danger">
                                <div className="progress-bar" role="progressbar" style={{ width: widthProgress + `%` }} aria-valuenow="75" aria-valuemin="0" aria-valuemax="100">
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
        )
    }
}

const mapDispatchToProps = dispatch => ({ 
    dispatch                
 })

export default connect(null, mapDispatchToProps)(DashboardSocialFeeds);