import React, { Component } from 'react' 
import { OverlayTrigger, Tooltip } from 'react-bootstrap';
import { Link } from 'react-router-dom';
export default class DashboardShoppableGalleries extends Component {
    render() {
        const { numberOfActiveWalls, authorized } = this.props;
        let widthProgress = parseInt((numberOfActiveWalls / (authorized && authorized.userData && authorized.userData.user_data ? authorized.userData.user_data.UserRule.walls : 100)) * 100);
        return ( 
                <div className="card card-custom card-stretch gutter-b home-widget-section">
                    <div className="wall_progress card-body">
                        <div className="d-flex align-items-center justify-content-between">
                        <h4 className="card-title">Gallery Count</h4>
                            {/* <Link to={`/content/moderation/${localStorage.getItem('wallId')}`} className="btn text-primary p-0">View</Link> */}
                            <OverlayTrigger placement='top' overlay={ <Tooltip> No. of galleries created vs the total no. of galleries available in your plan. </Tooltip> } >
                                    <img src="https://cloud.taggbox.com/taggshop/app/assets/media/dashboard/icons/tooltip-icon.svg" alt="tooltip-icon" />
                             </OverlayTrigger>
                        </div>
                        <h3>{numberOfActiveWalls}/{authorized?.userData?.user_data?.UserRule?.walls}</h3>

                        <div className="progress_range">
                            <div className="progress bar_success mb-0">
                                <div className="progress-bar" role="progressbar" style={{ width: widthProgress + `%` }} aria-valuenow="25" aria-valuemin="0" aria-valuemax="100">
                                </div>
                            </div>
                        </div>
                    </div>
                </div> 
        )
    }
}