import React, { Component } from 'react'
import { Button, Dropdown, Modal } from 'react-bootstrap';
import { CONNECT_FEED_HOME_ACTION } from '../../../../actions/sceneActions'
import { TOAST_SUCCESS, TOAST_ERROR } from '../../../../utils'
import { connect } from 'react-redux';
import { CLOUDPATH_TAGGSHOP, HOST_URL_TAGGSHOP } from '../../../../actions/api' 
import { CREATE_FEED } from '../../../../actions/moderationActions';

 class ConectFeedPopup extends Component {
     state={feedName:'', noFeedError:'', loader:false}

 creatFeed=(networkId)=>{
     const {feedName}= this.state;
     if(feedName){
         this.setState({loader:true})
         var obj = { wallId: localStorage.getItem('wallId'), networkId: 18, moderation: 0, account: 0, feedText:feedName, hashtagCaption: 0, filter: 23, accountId: 0, hashtagIGTV: 0, onlyReels: 0, }

        CREATE_FEED(obj, this.props.navigate, true, this.props.closeConnectPopup)
        // CONNECT_FEED_HOME_ACTION(feedName , networkId).then((response) => {
        //     const { code, message } = response.data;
        //     this.setState({loader:false})
        //     if (code == 200) {
        //         TOAST_SUCCESS(message);
        //         window.location.replace(`${HOST_URL_TAGGSHOP}/content/moderation/${localStorage.getItem('wallId')}`)
        //     }
        //     else if(code == 404){
        //         this.callbackMsg(message)
        //     }
        // }).catch((error) => {
        //     const { data } = error.response;
        //     if (data.responseCode == 404) { this.setState({ setState: false}) }
        //     console.log(error);
        //     TOAST_ERROR('Something went wrong');
        // });
        
        // this.props.CONNECT_FEED_HOME_ACTION(feedName,networkId, this.onCreatedFeed()); 
     }
     else{ this.callbackMsg('Please Enter handle name')}
    }
    
    callbackMsg=(msg)=>{
        this.setState({noFeedError:msg}, () => { setTimeout(() => { this.setState({ noFeedError:'' }) }, 5000) })
    }

    render() {
        const {closeConnectPopup, connectFeedId } = this.props
        const { feedName, noFeedError, loader} = this.state
        return (
            <Modal size="lg" dialogClassName="modal-dialog-centered ConnectFeedModal" show={true} onHide={closeConnectPopup}>
                <Modal.Header closeButton className="border-0">
                    <Modal.Title></Modal.Title>
                </Modal.Header>
                <Modal.Body className="pt-0">
                    {connectFeedId==18 ?<div className="d-flex align-items-center">
                        <div className="symbol symbol-50 mr-5">
                            <img src={`${CLOUDPATH_TAGGSHOP}/assets/media/square-icons/instagram-business.svg`} alt="Instagram" className="img-fluid"/>
                        </div>
                        <div className="">
                            <h2 className="mb-2 font-weight-boldest">Instagram</h2>
                            <p className="font-weight-boldest mb-0">Add your instagram business handle</p>
                        </div>
                    </div> :''}
                    <div className="mt-10">
                        <div className="input-group">
                            <div className="input-group-prepend">
                                <span className="input-group-text font-weight-boldest">Handle(@)</span>
                            </div>
                            <input className="form-control instaValue" value={this.state.feedName} type="text" id="instaValue" onChange={(e)=>this.setState({feedName:e.target.value})}/>
                        </div>
                        <span style={{color:'#ff0900'}}>{noFeedError}</span>
                    </div>
                    <div className="d-block text-right mt-10">
                        <button className={`btn btn-primary ${loader ?`spinner spinner-white spinner-right disabled`:``}`} 
                        onClick={()=>this.creatFeed(connectFeedId)}
                        // onClick={()=>closeConnectPopup}
                        >
                         Create Feed </button>
                    </div> 
                    
                </Modal.Body>
            </Modal>
        )
    }
}

const mapStateToProps = state => {
    return {
      authorized: state.authorized,
    }
  }



  export default connect(mapStateToProps)(ConectFeedPopup)