import React, { Component } from 'react';
import { Tab, Tabs } from "react-bootstrap";

import './onboarding.css'
import FeedPage from './components/FeedPage';
import ChooseNetwork from './components/ChooseNetwork';
import SyncProduct from './components/SyncProduct';
import FeedCreated from './components/FeedCreated';
import { GET_ADD_FEED, getModeration } from '../../../actions/moderationActions';
import { connect } from 'react-redux';
import { feedFilters } from './constant';
import { SignNoLeftTurnFill } from 'react-bootstrap-icons';
import Swal from 'sweetalert2';
import withRouter from '../../routes/WithRouter';
import { CLOUDPATH_TAGGSHOP } from '../../../actions/api';
import { emitEvent, onEvent } from '../../../actions/socketClient';
import { getUserInfo_afterChanges } from '../../../actions/loginActions';

const ob_step = {
  "chooseNetwork": 1,
  "addContent": 2,
  "syncProduct": 3,
}

class OnBoarding extends Component {
  state = { addclass: true, contentObject: null, activeTab: '', feedCreated: false, selectedNetworkId: null, stepsCompleted: [] };

  componentDidMount() {
    const { GET_ADD_FEED, navigate, getUserInfo_afterChanges } = this.props
    GET_ADD_FEED()
    setTimeout(() => {
      this.checkCompletedSteps()
    }, 500);
    // onEvent(`getOnboardingStep${localStorage.getItem("ownerId")}`, (data) => {
    //   console.log('onEvent')
    //   if(data.step == 2.5) this.setState({feedCreated: true}) 
    //   if(data.step == 4) navigate(`/content/moderation/${localStorage.getItem('wallId')}?ob_success=true`)
    //   if(data.step == 0) navigate(`/walls/home`)
    //   else getUserInfo_afterChanges(this.checkCompletedSteps)
    // });
  }

  checkCompletedSteps = () => {
    const { homeData } = this.props
    // console.log('homeData', homeData)
    if(!homeData.CHOOSE_NETWORK) this.setState({activeTab: "chooseNetwork"})
    if(homeData.CHOOSE_NETWORK && !homeData.FEED_CREATED) this.setState({activeTab: "addContent" }, () => this.onNetworkSelect(homeData.CHOOSE_NETWORK))
    if (homeData.FEED_CREATED) {
      if(!homeData.CHOOSE_NETWORK) this.props.updateExtraData({CHOOSE_NETWORK:true})
      if(!homeData.PRODUCT_ADDED) this.setState({activeTab: "syncProduct", stepsCompleted: [this.state.stepsCompleted, 'chooseNetwork', 'addContent']})
      // this.setState({ stepsCompleted: [this.state.stepsCompleted, 'chooseNetwork', 'addContent'], activeTab: "syncProduct" })
    }
    if (homeData.PRODUCT_ADDED) {
      if(!homeData.FEED_CREATED) this.setState({activeTab: "chooseNetwork", stepsCompleted: [...this.state.stepsCompleted, "syncProduct"]})
    } 
    if(!homeData.FEED_CREATED && !homeData.PRODUCT_ADDED && !homeData.CHOOSE_NETWORK) this.setState({activeTab: "chooseNetwork"})
  // this.setState({ stepsCompleted: [...this.state.stepsCompleted, "syncProduct"], activeTab: 'chooseNetwork' })
}

  onNetworkSelect = (id) => {
    const { updateExtraData } = this.props
    // console.log('filters', feedFilters[id])
    this.setState({ contentObject: feedFilters[id], activeTab: "addContent", selectedNetworkId: id },() => updateExtraData({CHOOSE_NETWORK: id}))
  }

  synctab = (name, icon) => {
    return <p><img src={`https://cloud.taggbox.com/taggshop/app/assets/media/onboarding-icons/${icon}.svg`} alt={name} /> {name}</p>
  }

  onNextStep = () => {
    const { activeTab, stepsCompleted } = this.state
    if(activeTab == "addContent"){
      if(!stepsCompleted.includes("syncProduct")) this.setState({activeTab: "syncProduct"},() => {this.props.updateExtraData({FEED_CREATED:true});getModeration()})
      else {
        // emitEvent('onboardingStep', { id: localStorage.getItem("ownerId"), step: 4 });
        setTimeout(() => {
          this.props.navigate(`/content/moderation/${localStorage.getItem("wallId")}?ob_success=true`)
        }, 1000);
      }

    }
    // 
  }

  onSkipSteps = () => {
    const { updateExtraData, homeData } = this.props
    updateExtraData({SKIP: true})
    if(homeData.FEED_CREATED) {
      // emitEvent('onboardingStep', { id: localStorage.getItem("ownerId"), step: 4 });
      this.props.navigate(`/content/moderation/${localStorage.getItem("wallId")}?ob_success=true`)
    }
    // else emitEvent('onboardingStep', { id: localStorage.getItem("ownerId"), step: 0 });
  }

  onMoreTab = () => {
    const { updateExtraData } = this.props
    Swal.fire({
      title: "You have to connect your Account to use more options",
      // text: "You have to connect your Instagram Business Account to use more options",
      icon: "warning",
      showCancelButton: true,
      confirmButtonText: "I'll do it later",
      cancelButtonText:"Proceed"
    }).then((result) => {
      if (result.isDismissed && result.dismiss == "cancel") {
        localStorage.setItem("selectedNetwork", this.state.selectedNetworkId)
        updateExtraData({SKIP: true})
        this.props.navigate(`/content/addfeed/${localStorage.getItem("wallId")}`)
      }
    });
  }

  componentDidUpdate(prevProps, prevState) {
    if (prevState.activeTab !== this.state.activeTab) {
      console.log(`Active tab changed from ${prevState.activeTab} to ${this.state.activeTab}`);
      // emitEvent('onboardingStep', { id: localStorage.getItem("ownerId"), step: ob_step[this.state.activeTab] });
    }
  }

  render() {
    const { addclass, contentObject, activeTab, feedCreated, selectedNetworkId } = this.state;
    const { addFeedData, homeData, updateExtraData, getModeration } = this.props
    return (
      <div className="onboarding_wrapper">
        <div className="onboard_main">
          <div className="onboardTab_header">
            <button className="onboarding_btn" onClick={this.onSkipSteps}>Skip</button>
            <ul className="nav nav-tabs" id="myTab" role="tablist">
              <li className="nav-item" role="presentation">
                <button className={`nav-link ${activeTab == 'chooseNetwork' ? "active" : ''} ${['addContent', 'syncProduct' ].includes(activeTab) ? 'completed' : ''}`} id="chooseNetwork-tab"  data-bs-target="#chooseNetwork" type="button" role="tab" aria-controls="ChooseNetwork" aria-selected="true" >
                  <span className="obStepNumber">
                    {['addContent', 'syncProduct'].includes(activeTab) ?
                      <img src={`${CLOUDPATH_TAGGSHOP}/app/assets/media/svg/icons/check-icon-white.svg`} alt='check_icon' />
                      :
                      1}
                  </span>
                  <p className="obStepLabel">Choose Network</p>
                </button>
                <img src="https://cloud.taggbox.com/taggshop/app/assets/media/angle-thin-right.svg" />
              </li>
              <li className="nav-item" role="presentation">
                <button className={`nav-link ${activeTab == 'addContent' ? "active" : ''} ${['syncProduct' ].includes(activeTab) ? 'completed' : ''}`} id="addContent-tab"  data-bs-target="#addContent" type="button" role="tab" aria-controls="addContent" aria-selected="false" >
                  <span className="obStepNumber">
                    {['syncProduct'].includes(activeTab) ?
                      <img src={`${CLOUDPATH_TAGGSHOP}/app/assets/media/svg/icons/check-icon-white.svg`} alt='check_icon' />
                      :
                      2}
                  </span>
                  <p className="obStepLabel">Add Content</p>
                </button>
                <img src="https://cloud.taggbox.com/taggshop/app/assets/media/angle-thin-right.svg" />
              </li>
              <li className="nav-item" role="presentation">
                <button className={`nav-link ${activeTab == 'syncProduct' ? "active" : ''}`} id="syncProduct-tab"  data-bs-target="#syncProduct" type="button" role="tab" aria-controls="syncProduct" aria-selected="false">
                  <img src={`${CLOUDPATH_TAGGSHOP}/app/assets/media/svg/icons/check-icon-white.svg`} alt='check_icon' />
                  <span className="obStepNumber">3</span>
                  <p className="obStepLabel">Sync Product</p>
                </button>
              </li>
            </ul>
          </div>
          <div className="onboardTab_tabsContent">
            <div className="tab-content">
              <div className={`tab-pane ${activeTab == 'chooseNetwork' ? 'active' : ''}`} id="chooseNetwork" role="tabpanel" aria-labelledby="chooseNetwork-tab">
                <div className="tabPane_inner">
                  <ChooseNetwork addFeedData={addFeedData} onNetworkSelect={this.onNetworkSelect} />
                </div>
              </div>
              <div className={`tab-pane ${activeTab == 'addContent' ? 'active' : ''}`} id="addContent" role="tabpanel" aria-labelledby="addContent-tab">
                <div className="tabPane_inner">
                  {!feedCreated ? contentObject ? <FeedPage contentObject={contentObject} onBack={() => this.setState({ activeTab: "chooseNetwork" },() => updateExtraData({CHOOSE_NETWORK: false}))} onNext={() => this.setState({ feedCreated: true })} selectedNetworkId={selectedNetworkId} onMoreTab={this.onMoreTab} /> : <></>
                    :
                    <FeedCreated onNext={this.onNextStep} />
                  }
                </div>
              </div>
              <div className={`tab-pane ${activeTab == 'syncProduct' ? 'active' : ''}`} id="syncProduct" role="tabpanel" aria-labelledby="syncProduct-tab">
                <div className="tabPane_inner">
                  <SyncProduct updateExtraData={updateExtraData} />
                </div>
              </div>
            </div>
            <div className="obSupportBottom">
              <p className="obSupportText"> In case of any query reach out to us at support@tagshop.ai or Talk to expert</p>
              <div className="obSupport_btns">
                <button onClick={() => window.open('https://meetings.hubspot.com/prakhar-from-taggshop', '_blank')}>
                  <svg xmlns="http://www.w3.org/2000/svg" width="15" height="15" viewBox="0 0 15 15" fill="none">
                    <path d="M13.9727 11.332L13.3164 14.0938C13.2344 14.5039 12.9062 14.7773 12.4961 14.7773C5.60547 14.75 0 9.14453 0 2.25391C0 1.84375 0.246094 1.51562 0.65625 1.43359L3.41797 0.777344C3.80078 0.695312 4.21094 0.914062 4.375 1.26953L5.66016 4.25C5.79688 4.60547 5.71484 5.01562 5.41406 5.23438L3.9375 6.4375C4.86719 8.32422 6.39844 9.85547 8.3125 10.7852L9.51562 9.30859C9.73438 9.03516 10.1445 8.92578 10.5 9.0625L13.4805 10.3477C13.8359 10.5391 14.0547 10.9492 13.9727 11.332Z" fill="#4E69ED" />
                  </svg>
                  Demo Call
                </button>
                <a href='https://vimeo.com/912907290?share=copy' data-lity >
                  <svg xmlns="http://www.w3.org/2000/svg" width="14" height="15" viewBox="0 0 14 15" fill="none">
                    <path d="M5.14062 4.79688C5.33203 4.66016 5.60547 4.66016 5.79688 4.79688L9.73438 7.20312C9.92578 7.3125 10.0625 7.53125 10.0625 7.75C10.0625 7.99609 9.92578 8.21484 9.73438 8.32422L5.79688 10.7305C5.60547 10.8398 5.33203 10.8672 5.14062 10.7305C4.92188 10.6211 4.8125 10.4023 4.8125 10.1562V5.34375C4.8125 5.125 4.92188 4.90625 5.14062 4.79688ZM14 7.75C14 11.6328 10.8555 14.75 7 14.75C3.11719 14.75 0 11.6328 0 7.75C0 3.89453 3.11719 0.75 7 0.75C10.8555 0.75 14 3.89453 14 7.75ZM7 2.0625C3.85547 2.0625 1.3125 4.63281 1.3125 7.75C1.3125 10.8945 3.85547 13.4375 7 13.4375C10.1172 13.4375 12.6875 10.8945 12.6875 7.75C12.6875 4.63281 10.1172 2.0625 7 2.0625Z" fill="#4E69ED" />
                  </svg> 
                  Watch Video
                </a>

                {/* <button>
                  <svg xmlns="http://www.w3.org/2000/svg" width="14" height="15" viewBox="0 0 14 15" fill="none">
                    <path d="M5.14062 4.79688C5.33203 4.66016 5.60547 4.66016 5.79688 4.79688L9.73438 7.20312C9.92578 7.3125 10.0625 7.53125 10.0625 7.75C10.0625 7.99609 9.92578 8.21484 9.73438 8.32422L5.79688 10.7305C5.60547 10.8398 5.33203 10.8672 5.14062 10.7305C4.92188 10.6211 4.8125 10.4023 4.8125 10.1562V5.34375C4.8125 5.125 4.92188 4.90625 5.14062 4.79688ZM14 7.75C14 11.6328 10.8555 14.75 7 14.75C3.11719 14.75 0 11.6328 0 7.75C0 3.89453 3.11719 0.75 7 0.75C10.8555 0.75 14 3.89453 14 7.75ZM7 2.0625C3.85547 2.0625 1.3125 4.63281 1.3125 7.75C1.3125 10.8945 3.85547 13.4375 7 13.4375C10.1172 13.4375 12.6875 10.8945 12.6875 7.75C12.6875 4.63281 10.1172 2.0625 7 2.0625Z" fill="#4E69ED" />
                  </svg>
                  Watch Video
                </button> */}
              </div>
            </div>
          </div>
        </div>
      </div>
    )
  }
}

const mapStateToProps = (state) => {
  return {
    addFeedData: state.addFeedReducer.addFeedNetworks,
  }
}

const mapDispatchToProps = dispatch => {
  return {
    GET_ADD_FEED: (callback) => dispatch(GET_ADD_FEED(callback)),
    getModeration: () => dispatch(getModeration()),
    getUserInfo_afterChanges: (callBack) => dispatch(getUserInfo_afterChanges(callBack))
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(OnBoarding))