import React, { Component } from 'react'
import { connect } from 'react-redux';
import Dropdown from 'react-bootstrap/Dropdown';
import DropdownButton from 'react-bootstrap/DropdownButton';
import { DASHBOARD_GALLERY_ANALYTICS, GET_DASHBOARD_DATA } from '../../../../actions/sceneActions';
import { getModeration } from '../../../../actions/moderationActions';
import { kFormatter } from '../../../../utils';
import { Link } from 'react-router-dom';
import withRouter from '../../../routes/WithRouter';
import ErrorFound from '../../../ErrorFound';
import { OverlayTrigger, Tooltip } from 'react-bootstrap';
import moment from 'moment';

 class TabContentGallery extends Component {

    state = {   activeGallery: [] }

    componentDidMount() { 
       this.loadgalleryData()
    }
  
    componentDidUpdate(prevProps){
        if(this.props.allWalls !== prevProps.allWalls) this.loadgalleryData()
    }

    loadgalleryData=()=>{
      if (localStorage.getItem('wallId')) {  
        var gallery = this.props.allWalls?.find((item) => item.id == localStorage.getItem('wallId')); 
        this.setState({ activeGallery: gallery }) 
    } else { 
        let firstWallId = this.props.allWalls && this.props.allWalls.length ? this.props.allWalls[0] : ''  
        this.setState({ activeGallery: firstWallId })
    } 
    }

    ChangeGallery = (e) => { 
        const { allWalls, start, end } = this.props
        let startDate = moment(start,"DD-MM-YYYY").startOf('day').format("YYYY-MM-DD HH:mm:ss");
        let endDate = moment(end, "DD-MM-YYYY").endOf('day').format("YYYY-MM-DD HH:mm:ss");
        var gallery = allWalls?.find((item) => item.id == e.id);
        this.setState({ activeGallery: gallery })
        this.props.DASHBOARD_GALLERY_ANALYTICS(e.id, startDate, endDate)
    }
   viewDetails=(id)=>{
    localStorage.setItem('wallId',id)
    this.props.navigate(`/content/moderation/${id}`)
   }
   

  render() {
    const {activeGallery} = this.state 
    const {homeData} = this.props 
    return (
      <>
       <div className="d-flex align-items-center justify-content-end mp_veiwDetails">
                        <button className="viewDetails"  onClick={()=>this.viewDetails(activeGallery?.id)}>view details</button>
                      {activeGallery ?  <DropdownButton id="dropdown-basic-button" title={activeGallery?.name}>
                         {this.props.allWalls?.map((item, i) => { return  <Dropdown.Item key={i} onClick={()=>this.ChangeGallery(item)}>{item?.name}</Dropdown.Item>}) }
                        </DropdownButton>:''}

                        
                      </div>
                      <div className="col-sm-12 custom-padding">
                        <div className="row"> 
                          {homeData?.content_gallery?.map((item , i)=>{ 
                            return <div className="col-sm-6 col-md-6 col-lg-3 custom-padding" key={i}>
                                      <div className="d-flex flex-wrap flex-column bs_box_outer">
                                        <div className="d-flex align-items-center justify-content-between box_top">
                                          <span className="bs_box_title">{item?.label} </span>
                                          <div className="bs_box_icon">
                                           <OverlayTrigger placement='top' overlay={ <Tooltip> {item?.tool_tip}</Tooltip> } >
                                            <img src="https://cloud.taggbox.com/taggshop/app/assets/media/dashboard/icons/tooltip-icon.svg" alt="tooltip-icon" />
                                            </OverlayTrigger> 
                                          </div>
                                        </div>
                                      <div className="d-flex align-items-center justify-content-start box_bottom"> 
                                      {homeData.content_gallery_loader ? <p className="sk_line sk_line_height_10 sk_line_20 m-0 mb-5"></p> :
                                          item?.networks && item?.networks.length > 0 ? <ul className="box_socialIcons">
                                            {item.networks.map((network , j)=>{ return <li key ={j}> <img src={network} alt="social-icon" /> </li> })}
                                          </ul>: <span className="bs_id_value">{item?.total }</span>}
                                           
                                      </div>
                                    </div>
                                  </div>
                          })} 
                        </div>
                      </div> 
                      </>
    )
  }
}

const mapStateToProps = state => {
    return {
      homeData: state.dashboardData,
      allWalls: state.dashboardData?.dashboarddata?.allWalls,
    }
  }
  
  const mapDispatchToProps = dispatch => {
    return { 
      DASHBOARD_GALLERY_ANALYTICS: (id, startDate, endDate) => dispatch(DASHBOARD_GALLERY_ANALYTICS(id, startDate, endDate)),
    }
  }
  
  export default connect(mapStateToProps, mapDispatchToProps)(withRouter(TabContentGallery));