import React, { Component } from 'react'
import { YOUTUBE_AUTOCOMPLETE } from '../../../../../actions/addFeedActions'
import YtSearchPlaces from './YtSearchPlaces'

export default class YoutubeInput extends Component {
    state = {
        list: [],
        error: false
    }


    onYoutubeChange = query => {
        const { onValueChange, tab } = this.props
        if ([1, 75, 11].includes(tab)) {
            try {
                const type = tab == 11 ? "playlist" : "channel"
                onValueChange(query)
                YOUTUBE_AUTOCOMPLETE(query, type).then(res => {
                    const { data, status } = res
                    if (status == 200) {
                        this.setState({ list: data.items })
                    }
                })
            } catch (error) {
                console.log(error)
            }
        }
        else onValueChange(query)
    }

    onListClick = (item) => {
        try {
            const { onValueChange, tab, onChannelIdChange, } = this.props
            onValueChange(tab == 11 ? item.snippet.title : item.snippet.channelTitle)
            onChannelIdChange(`${tab == 11 ? item.id.playlistId : tab == 75 ? item.id.channelId.replace("UC", "UUSH") : item.id.channelId}`)
            // if(document.querySelector('#feedsfield1'))
            document.getElementById(`feedsfield-${tab}`).value = `https://www.youtube.com/${tab == 11 ? "playlist?list=" : "channel/"}${tab == 11 ? item.id.playlistId : tab == 75 ? item.id.channelId.replace("UC", "UUSH") : item.id.channelId}`
            this.setState({ list: [] })
        } catch (error) {
            console.log("error------------------------", error)
        }
    }
    render() {
        const { tab, onValueChange, onLatlanChange, onKeywordChange, keyword } = this.props;
        const { list, error } = this.state;
        return (
            <div className="row">
                <div className={`col-md-${tab == 32 ? '6' : '12'}`}>
                    <div className="form-group">
                        {tab == 32 ?
                            <YtSearchPlaces onFieldTextChange={onValueChange} onLatlanChange={onLatlanChange} />
                            :
                            <div className="input-group">
                                <div className="input-group-prepend">
                                    <span className="input-group-text">
                                        <i className={`fas fa-${tab == 75 || tab == 1 ? 'at' : tab == 11 ? 'list' : tab == 4 ? "font" : tab == 32 ? "globe" : ""}`}></i>
                                    </span>
                                </div>
                                <input placeholder={`${tab == 32 ? "" : "Enter"} ${tab == 75 || tab == 1 ? "channel url" : tab == 11 ? "Play List ID" : tab == 4 ? "keyword" : ""}`} className="autosearch mycheck form-control " id={`feedsfield-${tab}`} type="text" onChange={e => this.onYoutubeChange(e.target.value)} />
                                {list && list.length ? <div className='handlelist' style={{ display: "block" }}>
                                    <ul className="list-unstyled">
                                        {list.map(item => (
                                            <li data-handle="FdgnlfsSdfgsg" key={item.etag} onClick={() => this.onListClick(item)}>
                                                <div className="img-section">
                                                    <img src={item.snippet.thumbnails.default.url} />
                                                </div>
                                                <div className="content-section"><span>{item.snippet.channelTitle}</span>
                                                    <p>{`https://www.youtube.com/${tab == 11 ? "playlist?list=" : "channel/"}${tab == 11 ? item.id.playlistId : item.id.channelId}`}</p>
                                                </div>
                                            </li>
                                        ))}

                                    </ul>
                                </div> : null}
                            </div>
                        }
                        {error ? <span id="feedsfield1error" className="errorMessage required">This field is required</span> : null}
                    </div>
                </div>
                {tab == 32 ?
                        <div className="col-md-6">
                            {/* <SearchPlace onFieldTextChange={onKeywordChange}/> */}
                            <div className="form-group">
                                <div className="input-group">
                                    <div className="input-group-prepend">
                                        <span className="input-group-text">
                                            <i className="fas fa-font"></i>
                                        </span>
                                    </div>
                                    <input placeholder="Enter keywords" id="" value={keyword} onChange={e => onKeywordChange(e.target.value)} className="form-control" type="text" />
                                </div>
                            </div>
                        </div>
                        : null
                    }
            </div>
        )
    }
}
