import store from "../store"
import { TOAST_ERROR, TOAST_SUCCESS } from "../utils"
import { CREATE_FEED, GET_COUNT, HOST_URL, ONBOARDING_CREATE_FEED, ONSITE_FEED } from "./api"
import HttpClient from "./http-client"
import { togglePriceSection } from "./show_modal_Action"

export const CREATE_OB_FEED = (data, callBack, onLoading) => {
    try {
        new HttpClient().post(CREATE_FEED, data, { validateStatus: false }).then(res => {
            const { responseCode, resposneMessage, response, postCount, response_code } = res.data
            localStorage.setItem('selectedNetwork', null)

            if (responseCode == 200 || response_code == 200) {
                TOAST_SUCCESS(resposneMessage)
                //  if(authorized && !authorized.FEED_CREATED){ 
                //      UPDATE_EXTRA_DATA({FEED_CREATED:true}).then(res => {
                //          store.dispatch(getUserInfo_afterChanges())
                //      })
                //     }
                 
                if (postCount != null && data.filter != 62) {
                    TOAST_SUCCESS(`${postCount} post added successfully`)
                }

                if (data.filter != 62 && response && response.length) {
                    GET_OB_POST_COUNT(response[0], data.networkId, callBack, onLoading)
                }
                if (data.filter == 62 && postCount && postCount.length) {
                    GET_OB_POST_COUNT(postCount[0], data.networkId, callBack, onLoading)
                }
            }
            else { 
                TOAST_ERROR(resposneMessage.message ? resposneMessage.message : resposneMessage)
                onLoading(false)
            }
        }).catch(err => {
            TOAST_ERROR("Some Error Occured!!")
            onLoading(false)
        })
    } catch (error) { 
        onLoading(false)
    }
}


export const GET_OB_POST_COUNT = (feedId, networkId, callBack, onLoading) => {
    const obj = {
        wallId: localStorage.getItem('wallId'),
        feedId,
        networkId: networkId
    }
    new HttpClient().post(GET_COUNT, obj, { validateStatus: false }).then(res => {
        const { responseCode, resposneMessage, postCount } = res.data
        if (responseCode == 200) {
            // store.dispatch(TAGGBOX_USER_HOME_DATA())
            TOAST_SUCCESS(`${postCount} ${postCount > 1 ? 'posts' : 'post'} added successfully`)
            onLoading(false)
            callBack()
        }
        else {TOAST_ERROR(resposneMessage.message ? resposneMessage.message : resposneMessage);onLoading(false)}

    
    }).catch(error => {
        onLoading(false)
    })
}

