export const CREATE_OB_ONSITE_FEED = (obj, callBack, onLoading) => {
    new HttpClient().post(ONSITE_FEED, obj, { validateStatus: false }).then(res => {
        const { responseCode, responseData, responseStatus } = res.data
        if (parseInt(responseCode) == 200) {
            TOAST_SUCCESS(responseData)
            callBack()
        }
        else TOAST_ERROR(responseData)
        if (responseStatus == "Upgrade") {
            setTimeout(() => {
                store.dispatch(togglePriceSection(true, 2))
            }, 1500);
        }
        onLoading(false)
        
    })
}