import React, { Component } from 'react'

export default class FeedCreated extends Component {
  render() {
    return (
      <div className="obFeedCreated_screen">
        <div className="obFeedCreated_Wrapper">
          <div className="obFeedCreatedIcon" style={{backgroundImage: 'url(https://cloud.taggbox.com/taggshop/app/assets/media/onboarding-icons/addContent-background.svg)'}}>
            <img src="https://cloud.taggbox.com/taggshop/app/assets/media/onboarding-icons/success-icon.svg" alt="success-iocn"/>
          </div>
          <h3>Congratulations 🎉</h3>
          <p><b>You have successfully created your first feed.</b> You can always change or add a new feed in the Content section</p>
          <button className="obFeedCreated_btn btn btn-primary" type="button" onClick={this.props.onNext}>Next</button>
        </div>
      </div>
    )
  }
}
